import { Link } from 'react-router-dom';
import ProductImage from '../assets/images/element-banner2-right.jpeg';
import Cement from '../assets/images/cement.png';
import POS from '../assets/images/pos.jpg';

const PosClient = ({clientKeywordSearch,addClient, editClient, delClient,selClient,showClientForm,content,filteredClients, payCredit, client}) => {
    
    return (
       
      <div className="col-xl-4 order-xl-first order-last">
      <div className="card card-custom gutter-b bg-white border-0">
         <div className="card-body">
            <div className="form-group row mb-0">
               <div className="col-md-12">
                  <label><b>CLIENTS/CUSTOMERS DATABASE</b></label><hr/>
                  <fieldset className="form-group mb-0 d-flex barcodeselection">
                     <button className="form-control w-50 btn-primary text-white" onClick={() => addClient()}>
                        <b>ADD CLIENT</b>
                     </button>&nbsp;
                     <input type="text" className="form-control border-dark" onChange={(e) => clientKeywordSearch(e)} placeholder="SEARCH CLIENT ..." />
                  </fieldset>
               </div>
            </div><br/>
            <div className="table-datapos">
               <div className="table-responsive" id="printableTable">
                  <div id="orderTable_wrapper" className="dataTables_wrapper no-footer">
                     <table id="orderTable" className="display dataTable no-footer" style={{width: '100%'}} role="grid">
                     <thead>
                        <tr role="row">
                           <th className="sorting_asc" rowSpan={1} colSpan={2} style={{width: '141px'}}>CUSTOMER NAME</th>
                           <th className="sorting" rowSpan={1} colSpan={1} style={{width: '117px'}}>LOCATION</th>
                           <th className="text-right no-sort sorting_disabled" rowSpan={1} colSpan={1} aria-label style={{width: '48px'}}>
                              <a className="btn btn-primary btn-sm py-1 px-3" title="Send Credit Sale" onClick={() => payCredit()}><small><i className="fas fa-credit-card text-warning" />&nbsp;<b className="text-warning">SUBMIT SALE</b></small></a>
                           </th>
                        </tr>
                     </thead>
                     <tbody>
                        {filteredClients && filteredClients.map((row) => (
                         
                        <tr role="row" className='odd'>
                           <td className="sorting_1" colSpan={2}>
                              <b>{row.name.toUpperCase()}</b> <br/>
                              <small style={{fontSize:'9px'}}><b><em class="text-primary">{row.phone}</em></b></small>
                           </td>
                           <td><small style={{fontSize:'9px'}}><b><em class="text-primary">{row.location}</em></b></small></td>
                           <td>
                              <div className="card-toolbar text-right btn-group">
                                 <a className={ row._id === client._id ? "btn btn-dark btn-sm py-1 px-3":"btn btn-warning btn-sm py-1 px-3"} title="Choose Client" onClick={() => selClient(row._id)}><i className={ row._id === client._id ? "fas fa-check text-warning" :"fas fa-user"} /></a>
                                 <a className="btn btn-success btn-sm py-1 px-3" title="Edit Client" onClick={() => editClient(row._id)}><i className="fas fa-edit" /></a>
                                 <a className="btn  btn-danger btn-sm py-1 px-3" title="Delete Client" onClick={() => delClient(row._id)}><i className="fas fa-trash-alt" /></a>
                              </div>
                           </td>
                        </tr>
                        ))
                       }
                     </tbody>
                     </table>
                  </div>
                 
               </div>
            </div>
            
            </div>
         </div>

         <div id="kt_notes_panel" className={showClientForm ? 'offcanvas offcanvas-right kt-color-panel p-5 offcanvas-on':'offcanvas offcanvas-right kt-color-panel p-5'}>
	         {content()}
		   </div>
         </div>
   )
}

export default PosClient
