import { Link } from 'react-router-dom';
import { useState, useEffect} from 'react';
import { withCookies } from 'react-cookie';
import moment from 'moment';


const LogStockPage = ({cookies}) => {

    const [ stocks, setStocks ] = useState([]);
    const [ filteredStocks, setFilteredStocks ] = useState([]);
    const { REACT_APP_API_URL } = process.env;
    const user = cookies.get('user');

    // Initialize
    useEffect(()=>{
        loadStock(); 
    },[]);

    // Keyword Search
    const keywordSearch = (e) => {
        var pros = stocks.filter( stock => moment(stock.created_at).format('LLL').toLowerCase().includes(e.target.value.trim().toLowerCase()));
        setFilteredStocks(pros);
    }
  
    // Load Stock
    const loadStock = async () => {
        const res = await fetch(`${REACT_APP_API_URL}/stocklogs`, {
          mode: 'cors',
        })
        const data = await res.json();
        if(data.success){
          setStocks(data.data);
          setFilteredStocks(data.data);
        }
    }


    /* PAGES */

   
    const ListPage = () => (
        <div>
            <div className="table-responsive" id="printableTable">
            <table id="productUnitTable" className="display dataTable no-footer" role="grid" aria-describedby="productUnitTable_info">
                <thead className="text-body">
                    <tr role="row">
                        <th>LOG DATE</th>
                        <th>ACTION</th>
                    </tr>
                </thead>
                <tbody className="kt-table-tbody text-dark">
                    {filteredStocks.map((stock,index) => (
                        <tr className="kt-table-row kt-table-row-level-0 odd" role="row" key={stock._id}>
                          <td><b>{stock.created_at ? stock.created_at.toUpperCase() : 'NONE'}</b></td>
                          <td><Link to={{pathname: '/stocklogview',state: { title: stock.created_at, meta: stock.meta} }} class="btn btn-xs btn-dark" data-toggle="tooltip" title="View Logs" data-placement="right"><small><i class="fas file-alt"></i>&nbsp;&nbsp; <b>VIEW LOG</b></small></Link></td>
                        </tr>
                    ))}
                    
                    </tbody>
                </table>
            </div> 
        </div>
    );

   
    return (
        <div className="d-flex flex-column-fluid pt-5">
            <div className="container-fluid pt-5">
                <div className="row">
                    <div className="col-12">
                        <div className="row">
                            <div className="col-lg-12 col-xl-12">
                                <div className="card card-custom gutter-b bg-transparent shadow-none border-0">
                                <div className="card-header align-items-center  border-bottom-dark px-0">
                                 
                                 {/* Page Title */}
                                <div className="card-title mb-0">
                                    <h3 className="card-label mb-0 font-weight-bold text-body">DAILY STOCK SNAPSHOTS</h3>
                                </div>

                                 {/* Actions */}
                                <div className="icons d-flex">
                                    <button className="btn ml-2 p-0"  onClick={() => null }>
                                        <span className="bg-dark h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle shadow-sm ">
                                            <i className="fa fa-plus text-white"></i> 
                                        </span>
                                    </button>
                               
                                </div>
                                </div>
                            </div>
                            </div>
                        </div>

                        {/* List Page */}
                        <div className="row">
                            <div className="col-12 ">
                                <div className="card card-custom gutter-b bg-white border-0">
                                    <div class="card-body">
                                        <div class="form-group row mb-0">
                                            <div class="col-md-12">
                                                <fieldset class="form-group mb-0 d-flex barcodeselection">
                                                   <input type="text" className="form-control border-dark" onChange={(e)=> keywordSearch(e)} placeholder="SEARCH ..."/>
                                                </fieldset>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="card-body">
                                       { ListPage() }
                                    </div>
                                </div>
                            </div>
                        </div>

                         {/* Form Page */}
                        <div id="kt_notes_panel" className={ false ? 'offcanvas offcanvas-right kt-color-panel p-5 offcanvas-on':'offcanvas offcanvas-right kt-color-panel p-5'}>
                           
                        </div>
                       
                    </div>
                </div>
            </div>
        </div>
       
    )
}




export default withCookies(LogStockPage)

