import { Link } from 'react-router-dom';
import { useState, useEffect} from 'react';
import Loader from '../assets/images/loadergif.gif';
//import {axios} from 'axios';

const UserPage = () => {

    const [ users, setUsers ] = useState([]);
    const [ showForm, setShowForm ] = useState(false);
    const [ showLoader, setShowLoader ] = useState(true);
    const [ formState, setFormState ] = useState('add');
    const [ formData, setFormData ] = useState({});
    const { REACT_APP_API_URL } = process.env;

    // Initialize
    useEffect(()=>{
        const getUsers = async () => {
            const usersFromServer = await fetchUsers();
            if(usersFromServer.success)  setUsers(usersFromServer.data);
            setShowLoader(false);
        }
        getUsers();
    },[]);

    // Fetch Users
    const fetchUsers = async () => {
       const res = await fetch(`${REACT_APP_API_URL}/users`,{ mode:'cors' })
       const data = await res.json();
       return data;
    }

     // Reset User Password
     const resetUser = async (id) => {
        var ins = window.prompt('Enter Password for user');
        if(ins){
            var ins2 = window.prompt('Enter Re-peat password for user');
            if(ins === ins2){
                const res = await fetch(`${REACT_APP_API_URL}/users/reset/${id}?pwd=${ins}`, {
                    mode: 'cors',
                    method: 'GET',
                })
                const data = await res.json();
                if(data.success) alert("Reset success!");
                console.log(data);
               
            }else{
              alert("Password mismatch");
            }
        }
       
    }

    // Delete User
    const deleteUser = async (id)=>{
        var ok = window.confirm("Delete User ? \n- This will remove an account user!");
        if(ok){
            const res = await fetch(`${REACT_APP_API_URL}/users/${id}`, {
                mode: 'cors',
                method: 'DELETE',
            })
            const data = await res.json();
            // if(data.success) setUsers(users.filter((user) => user.id !== id ))
            console.log(data);
            const usersFromServer = await fetchUsers();
            if(usersFromServer.success) setUsers(usersFromServer.data);
        }
    }


    // Edit User
    const editUser = async (id)=>{
        const res = await fetch(`${REACT_APP_API_URL}/users/${id}`, {mode: 'cors'})
        const data = await res.json();
        if(data.success){ setFormData(data.data); }
        console.log("Server Data: ",data.data);
        console.log("Set Data: ",formData);
        setFormState('edit');
        setShowForm(true);

    }

    // Add User
    const addUser = (id) => {
        window.document.getElementById("myform").reset();
        setFormData({});
        setFormState('add');
        setShowForm(true);
    }
    
    // Input handler
    const onChange = (e) => setFormData({ ...formData, [e.target.name]: e.target.value });

    // Save User
    const onSubmit = async (e) => {
        e.preventDefault();
        setShowLoader(true);
        try{
           const body = JSON.stringify(formData);
           const url = `${REACT_APP_API_URL}/users${formState === 'add' ? "": "/"+formData._id}`;
           const method = formState === 'add' ? "POST": "PUT";
           console.log('url: ',url);
           console.log('method: ',method);
           const res = await fetch(url,{
               headers: {"Content-Type": "application/json"},
               mode:    "cors",
               method:  method,
               body:    body
           });
           if(res.status === 200){
               const usersFromServer = await fetchUsers();
               if(usersFromServer.success){
                 setUsers(usersFromServer.data);
                 setShowForm(false);
               }
           }     setShowLoader(false);
           console.log(res);
        } catch (err) {
           setShowLoader(false);
           console.error(err);
        }
    };

    /* PAGES */

   
    const ListPage = () => (
        <div>
            <div className="table-responsive" id="printableTable">
            <table id="productUnitTable" className="display dataTable no-footer" role="grid" aria-describedby="productUnitTable_info">
                <thead className="text-body">
                    <tr role="row">
                        <th>USER NAME</th>
                        <th>EMAIL</th>
                        <th>FULL NAME</th>
                        <th>PHONE NUMBER</th>
                        <th>ROLE</th>
                        <th>STATUS</th>
                        <th>ACTION</th>
                    </tr>
                </thead>
                <tbody className="kt-table-tbody text-dark">
                    {users.map((user) => (
                        <tr className="kt-table-row kt-table-row-level-0 odd" role="row" key={user._id}>
                            <td>{user.username}</td>
                            <td>{user.email}</td>
                            <td>{user.name}</td>
                            <td>{user.phone}</td>
                            <td>{user.role}</td>
                            <td><span className="mr-0 text-success">{user.allow_access === 1 ? 'Active':'Inactive'}</span></td>
                            <td>
                                <div className="card-toolbar text-right">
                                <button className="btn p-0 shadow-none" type="button" id="dropdowneditButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    <span className="svg-icon">
                                    <svg width="20px" height="20px" viewBox="0 0 16 16" className="bi bi-three-dots text-body" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                        <path fillRule="evenodd" d="M3 9.5a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3z" />
                                    </svg>
                                    </span>
                                </button>
                                <div className="dropdown-menu dropdown-menu-right" aria-labelledby="dropdowneditButton" style={{position: 'absolute', transform: 'translate3d(1001px, 111px, 0px)', top: '0px', left: '0px', willChange: 'transform'}}>
                                    <button className="dropdown-item"  onClick={() => resetUser(user._id)} data-toggle="tooltip" title="Edit" data-placement="right">Reset Password</button>
                                    <button className="dropdown-item"  onClick={() => editUser(user._id)} data-toggle="tooltip" title="Edit" data-placement="right">Edit</button>
                                    <button className="dropdown-item" onClick={() => deleteUser(user._id)} title="Delete" >Delete</button>
                                </div>
                                </div>
                            </td>
                        </tr>
                    ))}
                    
                    </tbody>
                </table>
            </div>
        </div>
    );

    const FormPage = () => (
        <>
            <div className="offcanvas-header d-flex align-items-center justify-content-between pb-3">
                <h4 className="font-size-h4 font-weight-bold m-0">{ formState === 'add' ? 'ADD USER':'EDIT USER'} <hr/></h4>
                <button className="btn btn-sm btn-icon btn-light btn-hover-primary p-2" onClick={() => setShowForm(false)}><i className="fa fa-times"></i></button>
            </div>

            <form id="myform" onSubmit={(e) => onSubmit(e)} noValidate={true}>
              <div className="row">
                <div className="col-12">
                    <div className="form-group">
                        <label className="text-dark">User Name</label>
                        <input type="text" name="username" value={formData.username} readOnly={formState === 'edit'} onChange={onChange} className="form-control" />
                    </div>
                    <div className="form-group">
                        <label className="text-dark">Full Name</label>
                        <input type="text" name="name"  value={formData.name} onChange={onChange} className="form-control" />
                    </div>
                   
                    { formState === 'add' ? 
                     <div className="form-group">
                        <label className="text-dark">Password</label>
                        <input type="password" name="password" value={formData.password} onChange={onChange}  minLength='6' className="form-control" />
                     </div> : ''
                    }
                   
                    <div className="form-group">
                        <label className="text-dark">Role</label>
                        <select className="arabic-select w-100 mb-3 h-30px" name="role" onChange={onChange} value={formData.role}>
                            <option value="admin">Admin</option>
                            <option value="sales">Sales</option>
                            <option value="warehouse">Warehouse</option>
                        </select>
                    </div>
                    <div className="form-group">
                        <label className="text-dark">Email</label>
                        <input type="email" name="email" value={formData.email} onChange={onChange} className="form-control"/>
                    </div>
                    <div className="form-group">
                        <label className="text-dark">Phone Number</label>
                        <input type="text" name="phone" value={formData.phone} onChange={onChange} className="form-control" />
                    </div>
                    <div className="form-group">
                        <label className="text-dark">Address</label>
                        <input type="text" name="address" value={formData.address} onChange={onChange} className="form-control valid" aria-invalid="false" />
                    </div>
                    <div className="form-group">
                        <label className="text-dark">Location</label>
                        <input type="text" name="location" value={formData.location} onChange={onChange} className="form-control valid" required aria-invalid="false" />
                    </div>
                </div>
              </div>
              <button type="submit" className="btn btn-primary">SAVE</button>
            </form>
        </>
    );



    const LoaderPage = () => (
        <>
            <div className="se-pre-con" style={{opacity: 0.999993}}>
                <div className="pre-loader"><img class="img-fluid" src={Loader} alt="loading"/></div>
            </div>
        </>
    );
    



    return (
        <div className="d-flex flex-column-fluid pt-5">
            <div className="container-fluid pt-5">
                <div className="row">
                    <div className="col-12">
                        <div className="row">
                            <div className="col-lg-12 col-xl-12">
                                <div className="card card-custom gutter-b bg-transparent shadow-none border-0">
                                <div className="card-header align-items-center  border-bottom-dark px-0">
                                 
                                 {/* Page Title */}
                                <div className="card-title mb-0">
                                    <h3 className="card-label mb-0 font-weight-bold text-body">USERS</h3>
                                </div>

                                 {/* Actions */}
                                <div className="icons d-flex">
                                    <button className="btn ml-2 p-0"  onClick={() => addUser(2)}>
                                        <span className="bg-dark h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle shadow-sm ">
                                            <i className="fa fa-user-plus text-white"></i>
                                        </span>
                                    </button>
                                {/* 
                                    <Link to="#"  className="ml-2">
                                        <span className="icon h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle ">
                                            <svg width="15px" height="15px" viewBox="0 0 16 16" className="bi bi-printer-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                            <path d="M5 1a2 2 0 0 0-2 2v1h10V3a2 2 0 0 0-2-2H5z" />
                                            <path fillRule="evenodd" d="M11 9H5a1 1 0 0 0-1 1v3a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1v-3a1 1 0 0 0-1-1z" />
                                            <path fillRule="evenodd" d="M0 7a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v3a2 2 0 0 1-2 2h-1v-2a2 2 0 0 0-2-2H5a2 2 0 0 0-2 2v2H2a2 2 0 0 1-2-2V7zm2.5 1a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z" />
                                            </svg>
                                        </span>
                                    </Link>

                                    <Link to="#" className="ml-2">
                                        <span className="icon h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle ">
                                            <svg width="15px" height="15px" viewBox="0 0 16 16" className="bi bi-file-earmark-text-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                            <path fillRule="evenodd" d="M2 2a2 2 0 0 1 2-2h5.293A1 1 0 0 1 10 .293L13.707 4a1 1 0 0 1 .293.707V14a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V2zm7 2l.5-2.5 3 3L10 5a1 1 0 0 1-1-1zM4.5 8a.5.5 0 0 0 0 1h7a.5.5 0 0 0 0-1h-7zM4 10.5a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5zm0 2a.5.5 0 0 1 .5-.5h4a.5.5 0 0 1 0 1h-4a.5.5 0 0 1-.5-.5z" />
                                            </svg>
                                        </span>
                                    </Link>
                                */}
                                </div>
                                </div>
                            </div>
                            </div>
                        </div>

                        {/* List Page */}
                        <div className="row">
                            <div className="col-12 ">
                                <div className="card card-custom gutter-b bg-white border-0">
                                    <div className="card-body">
                                        {showLoader ? LoaderPage() : ListPage() }
                                    </div>
                                </div>
                            </div>
                        </div>

                         {/* Form Page */}
                        <div id="kt_notes_panel" className={showForm === true ? 'offcanvas offcanvas-right kt-color-panel p-5 offcanvas-on':'offcanvas offcanvas-right kt-color-panel p-5'}>
                            {FormPage()}
                        </div>
                       
                    </div>
                </div>
            </div>
        </div>
       
    )
}




export default UserPage
