import { useRef,useEffect } from 'react';
import { Link } from 'react-router-dom';
import Logo from '../assets/images/misc/logo.png';

const PosCart = (props) => {
  
   

    
    return (
      <div className="col-xl-5 col-lg-8 col-md-8">
                 <div className>
                    <div className="card card-custom gutter-b bg-white border-0 table-contentpos">
                       <div className="card-body">
                          <div className="d-flex justify-content-between colorfull-select">
                              <div><a href="#" onClick={() => props.payCredit()} class="btn btn-outline-dark "><i class="fas fa-credit-card mr-2"></i><b>PAY ON CREDIT</b></a></div>
                              <div><a href="#" onClick={() => props.payCash()} class="btn btn-dark white mb-2" data-toggle="modal" data-target="#payment-popup"><i class="fas fa-money-bill mr-2"></i><b>PAY WITH CASH</b></a></div>
                          </div>
                       </div>
                    </div>
                    { props.msg &&
                      <div className={props.msg.class}>
                        {props.msg.data}
                      </div>
                    }

                    <div className="card card-custom gutter-b bg-white border-0 table-contentpos">
                       <div className="table-datapos">
                          <div className="table-responsive" id="printableTable">
                             <div id="orderTable_wrapper" className="dataTables_wrapper no-footer">
                                <table id="orderTable" className="display dataTable no-footer" style={{width: '100%'}} role="grid">
                                <thead>
                                   <tr role="row"><th className="sorting_asc" tabIndex={0} aria-controls="orderTable" rowSpan={1} colSpan={1} aria-sort="ascending" aria-label="Name: activate to sort column descending" style={{width: '141px'}}>PRODUCT</th><th className="sorting" tabIndex={0} aria-controls="orderTable" rowSpan={1} colSpan={1} aria-label="Quantity: activate to sort column ascending" style={{width: '200px'}}>QUANTITY</th><th className="sorting" tabIndex={0} aria-controls="orderTable" rowSpan={1} colSpan={1} aria-label="Discount: activate to sort column ascending" style={{width: '123px'}}>PRICE (GHC)</th><th className="sorting" tabIndex={0} aria-controls="orderTable" rowSpan={1} colSpan={1} aria-label="Subtotal: activate to sort column ascending" style={{width: '117px'}}>AMOUNT (GHC).</th><th className="text-right no-sort sorting_disabled" rowSpan={1} colSpan={1} aria-label style={{width: '48px'}} /></tr>
                                </thead>
                                <tbody>
                                   {/*ct.isActive ? 'cartactive' : (ct.isDirty ? 'cartalert':'odd')*/}
                                  { props.cart && props.cart.map(ct => (
                                   <tr role="row" className={ct.isDirty ? 'cartalert' : (ct.isActive ? 'cartactive':'odd')} key={ct._id}>
                                      <td className="sorting_1">
                                         <b>{ct.title.toUpperCase()}</b> <br/>
                                         <small style={{fontSize:'9px'}}><b><em class="text-primary">STOCK: {ct.max}</em></b></small>
                                      </td>
                                      <td>
                                         <input type="text" defaultValue={ct.quantity} onChange={(e) => props.onChange(e,ct._id)} className="form-control border-dark w-100px" id="basicInput2" placeholder />
                                      </td>
                                      <td>{parseFloat(ct.price).toFixed(2)}</td>
                                      <td><b>{(parseFloat(ct.price) * parseFloat(ct.quantity)).toFixed(2)}</b></td>
                                      <td>
                                         <div className="card-toolbar text-right" onClick={() => props.removeFromCart(ct._id)}>
                                            <a className="confirm-delete" title="Delete"><i className="fas fa-trash-alt" /></a>
                                         </div>
                                      </td>
                                   </tr>
                                    )) 
                                 }

                                 {
                                     props.cart && props.cart == 0 ? (
                                      <tr><td colspan="5"><center><img src={Logo} style={{height:'230px', display:'block', margin:'0px auto', opacity: '0.3'}}/></center></td></tr>
                                    ): ''
                                 }
                                  
                                </tbody>
                                </table>
                             </div>
                             <div style={{ float:"left", clear: "both", height:'70px' }} ref={props.cartEnd}></div>
                          </div>
                       </div>
                       <div className="card-body">
                          <div className="form-group row mb-0">
                             {/* Testing mike */}

                     <div className="col-md-12 btn-submit d-flex justify-content-center">
                        <button type="submit" className="btn btn-sm btn-warning mr-2 py-2 px-3" title="Delete" onClick={(e) => props.saveSession(e)}>
                           <i className="fas fa-save mr-2" />
                           <small><b>SESSION</b></small>
                        </button>
                        
                        <button type="submit" className="btn btn-info btn-dark mr-2  py-2 px-3" onClick={() => props.clearSession()}>
                           <i className="fas fa-trash mr-2" />
                           <small><b>SESSION</b></small>
                        </button>

                        <button type="submit" className="btn btn-warning mr-2 py-2 px-3" title="Delete" onClick={(e) => props.makeInvoice(e)}>
                           <i className="fas fa-file-alt mr-2" />
                           <small><b>INVOICE</b></small>
                        </button>

                        <button type="submit" className="btn btn-dark mr-2 py-2 px-3" title="Delete" onClick={(e) => props.makeRequest(e)}>
                           <i className="fas fa-tasks mr-2" />
                           <small><b>REQUEST</b></small>
                        </button>
                     {/**/}
                  </div>

                          </div>
                       </div>
                    </div>
                 </div>
              </div>
    )
}

export default PosCart
