import { Link } from 'react-router-dom';
import { useState,useEffect } from 'react';
import { withCookies, Cookies } from "react-cookie";
import Moment from 'react-moment';
import moment from 'moment';
import Logo from '../assets/images/misc/logo.png';
import Paid from '../assets/images/paid1.jpeg';
import '../pages/Invoice.css';
import InvoiceCSS from '../components/InvoiceCss';
import { Bar, Pie, Line } from 'react-chartjs-2';



const SalesReportPage = () => {
    const from = moment(new Date()).format('YYYY-MM-DD');
    const to = moment(new Date()).format('YYYY-MM-DD');
    const { REACT_APP_API_URL } = process.env;
    const [ report, setReport ] = useState({});
    const [ orders, setOrders ] = useState([]);
    const [ period, setPeriod] = useState({to:`${to}`,from:`${from}`});
    const [ invoice, setInvoice ] = useState({});
    const [ showCover, setShowCover ] = useState(false);
   

    useEffect(()=>{
        const getReport = async () => {
          const dataFromServer = await fetchReport();
          if(dataFromServer.success){ 
            setReport(dataFromServer.data);
              setOrders(dataFromServer.data.daily);
              console.log(from);
          }
          
        }
        getReport();
    },[]);
    

    // Keyword Search
    const keywordSearch = (e) => {
      var pros = report.orders.filter( order => order.oid.toString().includes(e.target.value.trim().toLowerCase()) || moment(order.created_at).format('LLL').toLowerCase().includes(e.target.value.trim().toLowerCase()));
      setOrders(pros);
    }

    // Fetch Products
    const fetchReport = async () => {
        var query = `?from=${period.from}&to=${period.to}`;
        const res = await fetch(`${REACT_APP_API_URL}/report${query}`,{ mode:'cors' })
        const data = await res.json();
        return data;
    }

    // Fetch Receipt
    const fetchReceipt = async (id) => {
        const res = await fetch(`${REACT_APP_API_URL}/receipt/${id}`,{ mode:'cors' })
        const data = await res.json();
        if(data.success){
          // Set Invoice
          setInvoice(data.data);
          setShowCover(true);
          console.log(data.data);
        }
    }

    const onChange = async (e) => {
      setPeriod({...period,[ e.target.name ]: e.target.value})
    }

    const sendReport = async (e) => {
      e.preventDefault(); 
      console.log(period);
      const dataFromServer = await fetchReport();
      if(dataFromServer.success){ 
        setReport(dataFromServer.data);
        console.log(report); 
      }

    }

    const printSales = () => {
         var content = document.getElementById("print");
         var pri = document.getElementById("printer");
         const style = document.createElement("style");
         style.innerHTML = InvoiceCSS;
         pri.contentWindow.document.open();
         pri.contentWindow.document.write(content.innerHTML);//1
         pri.contentWindow.document.write(content.innerHTML);//2
         pri.contentDocument.head.appendChild(style)
         //console.log(pri.contentDocument.body)
         pri.contentWindow.document.close();
         pri.contentWindow.focus();
         pri.contentWindow.print();
    }

    const InvoicePage = () => (
      <div id="print">
          <div id="invoice-POS">
            <div className="offcanvas-header d-flex align-items-center justify-content-between pb-3">
                <h4 className="font-size-h4 font-weight-bold m-0">RECEIPT: # {invoice.oid}<hr/></h4>
                <button className="btn btn-sm btn-icon btn-light btn-hover-primary p-2" onClick={() => setShowCover(false)}><i className="fa fa-times"></i></button>
            </div>
                
                    <div className="info">
                      <h3>SALES RECEIPT</h3>
                    </div>
              
                <div id="mid">
                    <div>
                        <br/>
                        <p> 
                            <img src={Logo} style={{margin: '-20px 10px 0px 2px ', float: 'left'}} height="100px"/>
                            <b>
                                ADDRESS : Abura Market<br />
                                PHONE   :    &nbsp;&nbsp;054-043-8290 <br/>
                                MOBILE  :    &nbsp;&nbsp;026-928-5774 <br/>
                                INVOICE :  # {invoice.oid} <br/>
                                { invoice.created_at ? `DATE : ${invoice.created_at}`  : '' }
                                <br/>{ invoice.refname ? `CLIENT : ${invoice.refname.toUpperCase()}`  : '' }
                            </b>
                        </p><br/>
                    </div>
                </div>
                <div id="">
                  <div id="table">
                    <table>
                    <tbody>
                    <tr className="tabletitle">
                        <td>Item</td>
                        <td>Unit-Price</td>
                        <td>Qty</td>
                        <td>Sub Total</td>
                    </tr>
                  { invoice.cart ?
                    invoice.cart.map(inv => (
                        <tr className="service">
                            <td className="tableitem"><span className="itemtext">{inv.product && inv.product.title ? inv.product.title.toUpperCase() : 'NONE'}</span></td>
                            <td className="tableitem"><span className="itemtext">&#8373; {inv.price.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</span></td>
                            <td className="tableitem"><span className="itemtext">{inv.quantity}</span></td>
                            <td className="tableitem"><span className="itemtext">&#8373; {inv.amount.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</span></td>
                        </tr>
                      ))
                    : 
                    (
                        <tr className="service">
                            <td className="tableitem" COLSPAN="4"><br/><center className="itemtext"><h6 class="text-dark">NO ITEMS IN ORDER ...</h6></center><br/></td>
                        </tr>   
                    )
                  }
                  
                    <tr className="tabletitle shead">
                      <td colSpan="1"/>
                      <td className="Rate" align="right" colSpan="3">
                        <h2>Total :  &#8373;{ invoice.amount ? invoice.amount.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") : 0 }<br/><center><small><em><b>( VAT Inclusive )</b></em></small></center></h2>
                      </td>
                    </tr>
                    </tbody></table>
                </div>
                </div>
                <div>
                    <center style={{margin: '5px auto 0px'}}><b>GOODS SOLD ARE NOT RETURNABLE</b></center><hr/>
                    <center style={{margin: '0px auto'}}><b>THANKS FOR SHOPPING WITH US !!</b></center>
                    {Paid && <img src={Paid} style={{margin: '10px auto', display: 'block', height: '40px'}}/>}
                </div>
            </div>
            <center>
                <button class="btn btn-inline btn-dark btn-xs mx-5 center" onClick={() => printSales()}><i className="fas fa-print"></i>&nbsp;&nbsp;<b>PRINT RECEIPT</b></button>
            </center>
            <iframe id="printer" style={{height: '0px', width: '0px', position: 'absolute'}}></iframe>
           
      </div>
                        
    );

    // Monthly Sales Data Charts
    const monthdata = {
      labels: report.chartyear && Object.keys(report.chartyear),
      datasets: [
        {
          label: ' MONTHLY SALES',
          data: report.chartyear && Object.values(report.chartyear),
          backgroundColor: '#f49d2a',
          fill: true,
          borderColor: '#1a3353',
        }
      ],
    };

    // Weekly Sales Data Charts
    const weekdata = {
      labels: report.chartweek && Object.keys(report.chartweek),
      datasets: [
        {
          label: 'DAILY SALES',
          data: report.chartweek && Object.values(report.chartweek),
          backgroundColor: '#f49d2a',
          fill: true,
          borderColor: '#1a3353',
        }
      ],
    };
    
    

    
    return (
        <div className="content d-flex flex-column flex-column-fluid" id="tc_content">
                  {/*begin::Subheader*/}
                  <div className="subheader py-2 py-lg-6 subheader-solid">
                    <div className="container-fluid">
                      <nav aria-label="breadcrumb">
                        <ol className="breadcrumb bg-white mb-0 px-0 py-2">
                          <li className="breadcrumb-item active" aria-current="page">Dashboard</li>
                        </ol>
                      </nav>
                    </div>
                  </div>
                  {/*end::Subheader*/}
                  {/*begin::Entry*/}
                  <div className="d-flex flex-column-fluid">
                    {/*begin::Container*/}
                    <div className="container-fluid">
                      <div className="row">
                        <div className="col-12">

                        <div className="row">
                            <div className="col-lg-6 col-xl-6">
                              <div className="card card-custom gutter-b bg-white border-0 theme-circle theme-circle-secondary">
                                <div className="card-body">
                                  <h3 className="text-body font-weight-bold">PERIOD OF SALES :  { report.title }</h3>
                                </div>
                              </div>
                            </div>

                            <div className="col-lg-6 col-xl-6">
                              <div className="card card-custom gutter-b bg-white border-0 theme-circle theme-circle-secondary">
                                <div className="card-body">
                                 { true &&
                                  <form className="form-inline" onSubmit={sendReport}>
                                    <h3 className="text-body font-weight-bold">
                                      FROM : <input type="date" name="from" className="form-control border-dark" onChange={onChange} value={period.from} placeholder="Start Period"/>&nbsp;&nbsp;&nbsp;
                                      TO : <input type="date" name="to" className="form-control border-dark" onChange={onChange} value={period.to} placeholder="End Period"/>&nbsp;&nbsp;&nbsp;
                                      <button className="btn btn-danger "><b>FETCH</b></button>
                                    </h3>
                                    {/**/}
                                  </form>
                                  }
                                </div>
                              </div>
                            </div>



                         </div>

                        
                          <div className="row">
                            <div className="col-lg-4 col-xl-4">
                              <div className="card card-custom gutter-b bg-white border-0 theme-circle theme-circle-primary">
                                <div className="card-body">
                                  <h3 className="text-body font-weight-bold">CLIENT VISITS</h3>
                                  <div className="mt-3">
                                    <div className="d-flex align-items-center">
                                      <span className="text-dark font-weight-bold font-size-h1 mr-3"><span className="counter" data-target={400}>{report.clients}</span></span>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>
                            <div className="col-lg-4 col-xl-4">
                              <div className="card card-custom gutter-b bg-white border-0 theme-circle theme-circle theme-circle-secondary">
                                <div className="card-body">
                                  <h3 className="text-body font-weight-bold">QUANTITY SOLD</h3>
                                  <div className="mt-3">
                                    <div className="d-flex align-items-center">
                                      <span className="text-dark font-weight-bold font-size-h1 mr-3"><span className="counter" data-target={600}>{report.products}</span></span>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>

                           <div className="col-lg-4 col-xl-4">
                            <div className="card card-custom gutter-b bg-white border-0 theme-circle theme-circle-info">
                                <div className="card-body">
                                  <h3 className="text-body font-weight-bold">AMOUNT SOLD</h3>
                                  <div className="mt-3">
                                    <div className="d-flex align-items-center">
                                      <span className="text-dark font-weight-bold font-size-h1 mr-3">₵<span className="counter" data-target={6800}>{report.sales && report.sales.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</span></span>
                                      
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>


                         
                          <div className="row">
                            
                            <div className="col-lg-6 col-xl-6">
                              <div className="card card-custom gutter-b bg-white border-0">
                                <div className="card-header align-items-center  border-0">
                                  <div className="card-title mb-0">
                                    <h3 className="card-label font-weight-bold mb-0 text-body">{report.yeartitle}</h3>
                                    <div>
                                      <Line data={monthdata} height={320} width={480} options={{ maintainAspectRatio: false }} />
                                    </div>
                                  </div>
                                </div>

                                <div className="card-body">
                                    &nbsp;&nbsp;
                                </div>
                              </div>
                            </div>


                            <div className="col-lg-6 col-xl-6">
                              <div className="card card-custom gutter-b bg-white border-0">
                                <div className="card-header align-items-center  border-0">
                                  <div className="card-title mb-0">
                                    <h3 className="card-label font-weight-bold mb-0 text-body">{report.weektitle}</h3>
                                  </div>
                                </div>
                                <div className="card-body pt-3">
                                    <div>
                                      <Line data={weekdata} height={320} width={480} options={{ maintainAspectRatio: false }} />
                                    </div>
                                 </div>
                              </div>
                            </div>
                          </div>



                          <div className="row">
                            <div className="col-lg-12 col-xl-12">
                              <div className="card card-custom gutter-b bg-white border-0">
                                <div className="card-header align-items-center  border-0">
                                  <div className="card-title mb-0">
                                    <h3 className="card-label mb-0 font-weight-bold text-body">COMPLETED SALES - {report.title}</h3>
                                  </div>
                                  <div className="card-toolbar">
                                    <button className="btn p-0" type="button" id="dropdownMenuButton3" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                      <span className="svg-icon">
                                        <svg width="20px" height="20px" viewBox="0 0 16 16" className="bi bi-three-dots text-body" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                          <path fillRule="evenodd" d="M3 9.5a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3z" />
                                        </svg>
                                      </span>
                                    </button>
                                    <div className="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuButton3">
                                      <Link className="dropdown-item" to="#">Action</Link>
                                      <Link className="dropdown-item" to="#">Another action</Link>
                                      <Link className="dropdown-item" to="#">Something else here</Link>
                                    </div>
                                  </div>
                                </div>
                               
                                <div className="card-body">
                                       <div class="form-group row mb-0">
                                            <div class="col-md-12">
                                                <fieldset class="form-group mb-0 d-flex barcodeselection">
                                                    <input type="text" className="form-control border-dark" onChange={(e)=> keywordSearch(e)} placeholder="SEARCH DATABASE ..."/>
                                                </fieldset>
                                            </div>
                                        </div>
                                  <div>
                                  <div className="kt-table-content table-responsive">
                                      <table id="myTable2" className="table ">
                                        <thead className="kt-table-thead text-body">
                                          <tr>
                                            <th className="kt-table-cell">RECEIPT#</th>
                                            <th className="kt-table-cell">TYPE</th>
                                            <th className="kt-table-cell">AMOUNT</th>
                                            <th className="kt-table-cell">SALES DATE</th>
                                          </tr>
                                        </thead>
                                        <tbody className="kt-table-tbody text-dark">
                                          { report.orders && report.orders.map(order => (
                                          <tr className="kt-table-row">
                                            <td className="kt-table-cell"><b><em>#{order.oid}</em></b><br/>{order.refname ? <em>[ {order.refname} ]</em>:''}</td>
                                            <td className="kt-table-cell">
                                              <small className="d-flex align-items-center">
                                                <b className="ml-2">{order.ordertype == 'normal' ? 'CASH':'CREDIT'}</b></small>
                                            </td>
                                            <td className="kt-table-cell"><b>₵{order.amount && order.amount.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</b></td>
                                            <td className="kt-table-cell"><center><small style={{fontSize:'12px'}}><b>[ {order.created_at.toUpperCase()} ]</b></small></center></td>
                                            <td className="">
                                              <button className="btn-dark" onClick={() => fetchReceipt(order.oid)}><i className="fas fa-lg fa-book"></i></button>
                                            </td>
                                          </tr>
                                          ))}
                                        </tbody>
                                      </table>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>

                           </div>
                        
                         


                           {/* Form Page */}
                            <div id="kt_notes_panel" className={ showCover ? 'offcanvas offcanvas-right kt-color-panel offcanvas-on':'offcanvas offcanvas-right kt-color-panel'}>
                                {InvoicePage( )}
                            </div>
                       
                         
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                
    )
}

export default SalesReportPage
