import { Link } from 'react-router-dom';
import { useState, useEffect} from 'react';
import { withCookies } from "react-cookie";
import Loader from '../assets/images/loadergif.gif';
//import {axios} from 'axios';

const RestockPage = ({ cookies }) => {

    const [ stocks, setStocks ] = useState([]);
    const [ filteredStocks, setFilteredStocks ] = useState([]);
    const [ products, setProducts ] = useState([]);
    const [ showForm, setShowForm ] = useState(false);
    const [ formState, setFormState ] = useState('add');
    const [ formData, setFormData ] = useState({});
    const { REACT_APP_API_URL } = process.env;
    var user = cookies.get('user');

    // Initialize
    useEffect(()=>{
        const getStocks = async () => {
            const stocksFromServer = await fetchStocks();
            const productsFromServer = await fetchProducts();
            
            if(stocksFromServer.success && productsFromServer.success){
                var hdata = [...productsFromServer.data];
                setStocks(stocksFromServer.data);
                setFilteredStocks(stocksFromServer.data);
                setProducts(hdata);
            }
        }
        getStocks(); 
    },[]);

    // Keyword Search
    const keywordSearch = (e) => {
        var pros = stocks.filter( stock => stock.product.title.toLowerCase().includes(e.target.value.trim().toLowerCase()));
        setFilteredStocks(pros);
    }
  

    // Fetch Users
    const fetchStocks = async () => {
       const res = await fetch(`${REACT_APP_API_URL}/stocks`,{ mode:'cors' })
       const data = await res.json();
       return data;
    }

    const fetchProducts = async () => {
        const res = await fetch(`${REACT_APP_API_URL}/products`,{ mode:'cors' })
        const data = await res.json();
        return data;
     }

    // Delete User
    const deleteStock = async (id)=>{
        const res = await fetch(`${REACT_APP_API_URL}/stocks/${id}`, {
            mode: 'cors',
            method: 'DELETE',
        })
        const data = await res.json();
        const stocksFromServer = await fetchStocks();
        if(stocksFromServer.success){
            setStocks(stocksFromServer.data);
            setFilteredStocks(stocksFromServer.data);
        }
    }


    // Load Stock
    const loadStock = async (id)=>{
        const res = await fetch(`${REACT_APP_API_URL}/loadstock/${id}`, {
            mode: 'cors',
        })
        const data = await res.json();
        const stocksFromServer = await fetchStocks();
        if(stocksFromServer.success){
            setStocks(stocksFromServer.data);
            setFilteredStocks(stocksFromServer.data);
        }
    }

    // Edit Stock
    const editStock = async (id)=>{
        const res = await fetch(`${REACT_APP_API_URL}/stocks/${id}`, {mode: 'cors'})
        const data = await res.json();
        const mdata = { ...data.data };
        if(data.success) setFormData(mdata);
        setFormState('edit');
        setShowForm(true);
        console.log(data.success);
        console.log(formData);
    }

    // Add Stock
    const addStock = async () => {
        window.document.getElementById("myform").reset();
        setFormState('add');
        setShowForm(true);
        setFormData({});
        
    }
    
    // Input handler
    const onChange = (e) => setFormData({ ...formData, [e.target.name]: e.target.value });

    // Save Stock
    const onSubmit = async (e) => {
        e.preventDefault();
        try{
           const body = JSON.stringify(formData);
           const url = `${REACT_APP_API_URL}/stocks${formState === 'add' ? "": "/"+formData._id}`;
           const method = formState === 'add' ? "POST": "PUT";
           const res = await fetch(url,{
               headers: {"Content-Type": "application/json"},
               mode:    "cors",
               method:  method,
               body:    body
           });
           if(res.status === 200){
               const stocksFromServer = await fetchStocks();
               if(stocksFromServer.success){
                  setStocks(stocksFromServer.data);
                  setFilteredStocks(stocksFromServer.data);
                  setShowForm(false);
               }
           }    
        } catch (err) {
           console.error(err);
        }
    };

    /* PAGES */

   
    const ListPage = () => (
        <div>
            <div className="table-responsive" id="printableTable">
            <table id="productUnitTable" className="display dataTable no-footer" role="grid" aria-describedby="productUnitTable_info">
                <thead className="text-body">
                    <tr role="row">
                        <th>PRODUCT</th>
                        <th>SUPPLIER</th>
                        {/*
                        <th>SELLING PRICE</th>
                        <th>COST PRICE</th>
                        */}
                        <th>QUANTITY</th>
                        <th>STOCK DATE</th>
                        <th>STATUS</th> 
                        <th>ACTION</th>
                    </tr>
                </thead>
                <tbody className="kt-table-tbody text-dark">
                    {filteredStocks.map((stock,index) => (
                        <tr className="kt-table-row kt-table-row-level-0 odd" role="row" key={stock._id}>
                            <td><b>{stock.product ? stock.product.title.toUpperCase() : 'NONE'}</b></td>
                            <td><em>{stock.supplier}</em></td>
                            {/*
                            <td><b>GHC {stock.price}</b></td>
                            <td>GHC {stock.cprice}</td>
                            */}
                            <td>{stock.quantity}</td>
                            <td><b>{stock.created_at.toUpperCase()}</b></td>
                            <td><small><b className={stock.action === 1 ? 'mr-0 text-success':'mr-0 text-dark'}>{stock.action === 1 ? 'LOADED':'NOT LOADED'}</b></small></td>
                            <td>
                                { user.role === 'admin' ?
                                <>
                                { stock.action === 0 ?
                                <div className="card-toolbar text-right">
                                <button className="btn p-0 shadow-none" type="button" id="dropdowneditButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    <span className="svg-icon">
                                    <svg width="20px" height="20px" viewBox="0 0 16 16" className="bi bi-three-dots text-body" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                        <path fillRule="evenodd" d="M3 9.5a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3z" />
                                    </svg>
                                    </span>
                                </button>
                                { stock.action === 0 && (
                                <div className="dropdown-menu dropdown-menu-right" aria-labelledby="dropdowneditButton" style={{position: 'absolute', transform: 'translate3d(1001px, 111px, 0px)', top: '0px', left: '0px', willChange: 'transform'}}>
                                   { stock.action === 0 && (
                                    <button className="dropdown-item text-danger"  onClick={() => loadStock(stock._id)} data-toggle="tooltip" title="Edit" data-placement="right"><b>LOAD</b></button>
                                   )}

                                   { stock.action === 0 && (
                                    <button className="dropdown-item"  onClick={() => editStock(stock._id)} data-toggle="tooltip" title="Edit" data-placement="right">EDIT</button>
                                   )}
                                   { stock.action === 0 && (
                                    <button className="dropdown-item" onClick={() => deleteStock(stock._id)} title="Delete" >DELETE</button>
                                   )}
                                </div>
                                )}
                                </div>
                                : ''}
                                </> :  'NONE'}
                            </td>
                        </tr>
                    ))}
                    
                    </tbody>
                </table>
            </div>
        </div>
    );

    const FormPage = () => (
        <>
            <div className="offcanvas-header d-flex align-items-center justify-content-between pb-3">
                <h4 className="font-size-h4 font-weight-bold m-0">{ formState === 'add' ? 'ADD STOCK':'EDIT STOCK'} <hr/></h4>
                <button className="btn btn-sm btn-icon btn-light btn-hover-primary p-2" onClick={() => setShowForm(false)}><i className="fa fa-times"></i></button>
            </div>

            <form id="myform" onSubmit={onSubmit} noValidate={true}>
              <div className="row">
                <div className="col-12">
                    <div className="form-group">
                        <label className="text-dark">Product</label>
                        <select className="arabic-select w-100 mb-3 h-30px" name="product" onChange={onChange} value={formData.product && formData.product._id}>
                           <option>-- CHOOSE PRODUCT --</option>
                           { products && products.length > 0 ? products.map(product => (
                            <option value={product._id}>{product.title.toUpperCase()}</option>
                           )): ( <option>-- Not Loaded --</option>)}
                        </select>
                    </div>
                    {/*
                    <div className="form-group">
                        <label className="text-dark">Selling Price (GHC)</label>
                        <input type="text" name="price"  value={formData.price} onChange={onChange} className="form-control" />
                    </div>
                    <div className="form-group">
                        <label className="text-dark">Cost Price (GHC)</label>
                        <input type="text" name="cprice"  value={formData.cprice} onChange={onChange} className="form-control" />
                    </div>
                    */}
                    {/*readOnly={formState === 'edit'}*/ }
                    <div className="form-group">
                        <label className="text-dark">Quantity ( Being Added) </label>
                        <input type="text" name="quantity"  value={formData.quantity} onChange={onChange} className="form-control" />
                    </div>

                    <div className="form-group">
                        <label className="text-dark">Supplier</label>
                        <input type="text" name="supplier" value={formData.supplier}  onChange={onChange} className="form-control" />
                    </div>
                    
   
                 
                </div>
              </div>
              <button type="submit" className="btn btn-primary">SAVE</button>
            </form>
        </>
    );


    return (
        <div className="d-flex flex-column-fluid pt-5">
            <div className="container-fluid pt-5">
                <div className="row">
                    <div className="col-12">
                        <div className="row">
                            <div className="col-lg-12 col-xl-12">
                                <div className="card card-custom gutter-b bg-transparent shadow-none border-0">
                                <div className="card-header align-items-center  border-bottom-dark px-0">
                                 
                                 {/* Page Title */}
                                <div className="card-title mb-0">
                                    <h3 className="card-label mb-0 font-weight-bold text-body">WAREHOUSE STOCK DATABASE</h3>
                                </div>

                                 {/* Actions */}
                                <div className="icons d-flex">
                                    { user.role === 'admin' ?
                                    <button className="btn ml-2 p-0"  onClick={() => addStock()}>
                                        <span className="bg-dark h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle shadow-sm ">
                                            <i className="fa fa-plus text-white"></i> 
                                        </span>
                                    </button>
                                     : null }
                                {/* 
                                    <Link to="#"  className="ml-2">
                                        <span className="icon h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle ">
                                            <svg width="15px" height="15px" viewBox="0 0 16 16" className="bi bi-printer-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                            <path d="M5 1a2 2 0 0 0-2 2v1h10V3a2 2 0 0 0-2-2H5z" />
                                            <path fillRule="evenodd" d="M11 9H5a1 1 0 0 0-1 1v3a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1v-3a1 1 0 0 0-1-1z" />
                                            <path fillRule="evenodd" d="M0 7a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v3a2 2 0 0 1-2 2h-1v-2a2 2 0 0 0-2-2H5a2 2 0 0 0-2 2v2H2a2 2 0 0 1-2-2V7zm2.5 1a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z" />
                                            </svg>
                                        </span>
                                    </Link>

                                    <Link to="#" className="ml-2">
                                        <span className="icon h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle ">
                                            <svg width="15px" height="15px" viewBox="0 0 16 16" className="bi bi-file-earmark-text-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                            <path fillRule="evenodd" d="M2 2a2 2 0 0 1 2-2h5.293A1 1 0 0 1 10 .293L13.707 4a1 1 0 0 1 .293.707V14a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V2zm7 2l.5-2.5 3 3L10 5a1 1 0 0 1-1-1zM4.5 8a.5.5 0 0 0 0 1h7a.5.5 0 0 0 0-1h-7zM4 10.5a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5zm0 2a.5.5 0 0 1 .5-.5h4a.5.5 0 0 1 0 1h-4a.5.5 0 0 1-.5-.5z" />
                                            </svg>
                                        </span>
                                    </Link>
                                */}
                                </div>
                                </div>
                            </div>
                            </div>
                        </div>

                        {/* List Page */}
                        <div className="row">
                            <div className="col-12 ">
                                <div className="card card-custom gutter-b bg-white border-0">
                                    <div class="card-body">
                                        <div class="form-group row mb-0">
                                            <div class="col-md-12">
                                                <label><b>STOCKS</b></label>
                                                <fieldset class="form-group mb-0 d-flex barcodeselection">
                                                    <input type="text" className="form-control border-dark" onChange={(e)=> keywordSearch(e)} placeholder="SEARCH STOCK HISTORY ..."/>
                                                </fieldset>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="card-body">
                                       { ListPage() }
                                    </div>
                                </div>
                            </div>
                        </div>

                         {/* Form Page */}
                        <div id="kt_notes_panel" className={showForm === true ? 'offcanvas offcanvas-right kt-color-panel p-5 offcanvas-on':'offcanvas offcanvas-right kt-color-panel p-5'}>
                            {FormPage()}
                        </div>
                       
                    </div>
                </div>
            </div>
        </div>
       
    )
}




export default withCookies(RestockPage)

