import { Link } from 'react-router-dom';
import { useState, useEffect} from 'react';
import { withCookies, Cookies } from "react-cookie";
import Loader from '../assets/images/loadergif.gif';
import PageCSS from '../components/PageCss';
import moment from 'moment';
//import {axios} from 'axios';

const RequestPage = (props) => {

    const [ overview, setOverview ] = useState([]);
    const [ invoice, setInvoice ] = useState([]);
    const [ showCover, setShowCover ] = useState(false);
    const { REACT_APP_API_URL } = process.env;
    var user = props.cookies.get('user');

    useEffect(()=>{
        const getOverview = async () => {
          const dataFromServer = await fetchOverview();
          if(dataFromServer.success){ 
              setOverview(dataFromServer.data);
              setInvoice(dataFromServer.data);
              console.log(dataFromServer.data);
          }
        }
        getOverview();
    },[overview]);
   
    // Fetch Products
    const fetchOverview = async () => {
        const res = await fetch(`${REACT_APP_API_URL}/requests`,{ mode:'cors' })
        const data = await res.json();
        return data;
    }
    // Keyword Search
    const keywordSearch = (e) => {
        var pros = overview.filter( req => req.product.title.toLowerCase().includes(e.target.value.trim().toLowerCase()));
        setInvoice(pros);
    }

    const removeRequest = async (id) => {
        var ok = window.confirm('Remove requistion record ?');
        if(ok){
           const res = await fetch(`${REACT_APP_API_URL}/requests/${id}`, {
            mode: 'cors',
            method: 'DELETE',
        })
        const data = await res.json();
        const usersFromServer = await fetchOverview();
        if(usersFromServer.success) setInvoice(usersFromServer.data);
        }
    }
  

    const viewInvoice = (e) => {
        console.log(e.target.value);
    }

    const printData = () => {
        var content = document.getElementById("print");
        var pri = document.getElementById("printer");
        const style = document.createElement("style");
        style.innerHTML = PageCSS;
        pri.contentWindow.document.open();
        pri.contentWindow.document.write(content.innerHTML);//1
        pri.contentDocument.head.appendChild(style)
         //console.log(pri.contentDocument.body)
        pri.contentWindow.document.close();
        pri.contentWindow.focus();
        pri.contentWindow.print();
    }
    

    /* PAGES */

   
    const ListPage = () => (
        <div id="print">
            <h1 className="no-screen">REQUISITIONS</h1>
            <div className="table-responsive" id="printableTable">
            <table id="productUnitTable" className="display dataTable no-footer" role="grid" aria-describedby="productUnitTable_info">
                <thead className="text-body">
                    <tr role="row">
                        <th>REQUEST DATE</th>
                        <th>PRODUCT</th>
                        <th>QUANTITY</th>
                        <th>SALES RECEIPT</th>
                        <th>ISSUER</th>
                        <th>ACTION</th>
                    </tr>
                </thead>
                <tbody className="kt-table-tbody text-dark">
                    {invoice.map((inv,index) => (
                        <tr className="kt-table-row kt-table-row-level-0 odd" role="row" key={inv._id}>
                            <td><small style={{width:'9px'}}><b>{moment(inv.created_at).format('LLL').toUpperCase()}</b></small></td>
                            <td><b><em>{inv.product.title}</em></b></td>
                            <td><b>{inv.quantity}</b></td>
                            <td><b>#{inv.oid}</b></td>
                            <td><small><b>{inv.user.toUpperCase()}</b></small></td>
                            <td><button class="btn-dark" onClick={() => removeRequest(inv._id)}><i class="fas fa-lg fa-trash"></i></button></td>
                        </tr>
                    ))}
                    
                    </tbody>
                </table>
            </div>
            <iframe id="printer" style={{height: '0px', width: '0px', position: 'absolute'}}></iframe>
        </div>
    );


    return (
        <div className="d-flex flex-column-fluid pt-5">
            <div className="container-fluid pt-5">
                <div className="row">
                    <div className="col-12">
                        <div className="row">
                            <div className="col-lg-12 col-xl-12">
                                <div className="card card-custom gutter-b bg-transparent shadow-none border-0">
                                <div className="card-header align-items-center  border-bottom-dark px-0">
                                 
                                 {/* Page Title */}
                                <div className="card-title mb-0">
                                    <h3 className="card-label mb-0 font-weight-bold text-body">WAREHOUSE REQUISITIONS</h3>
                                </div>

                                 {/* Actions */}
                                <div className="icons d-flex">
                                    <Link to="#"  className="ml-2" onClick={()=> printData()}>
                                        <span className="icon h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle ">
                                            <svg width="15px" height="15px" viewBox="0 0 16 16" className="bi bi-printer-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                            <path d="M5 1a2 2 0 0 0-2 2v1h10V3a2 2 0 0 0-2-2H5z" />
                                            <path fillRule="evenodd" d="M11 9H5a1 1 0 0 0-1 1v3a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1v-3a1 1 0 0 0-1-1z" />
                                            <path fillRule="evenodd" d="M0 7a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v3a2 2 0 0 1-2 2h-1v-2a2 2 0 0 0-2-2H5a2 2 0 0 0-2 2v2H2a2 2 0 0 1-2-2V7zm2.5 1a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z" />
                                            </svg>
                                        </span>
                                    </Link>
                                </div>
                                </div>
                            </div>
                            </div>
                        </div>

                        {/* List Page */}
                        <div className="row">
                            <div className="col-12 ">
                                <div className="card card-custom gutter-b bg-white border-0">
                                    <div class="card-body">
                                        <div class="form-group row mb-0">
                                            <div class="col-md-12">
                                                <fieldset class="form-group mb-0 d-flex barcodeselection">
                                                    <input type="text" className="form-control border-dark" onChange={(e)=> keywordSearch(e)} placeholder="SEARCH REQUESTS ..."/>
                                                </fieldset>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="card-body">
                                       { ListPage() }
                                    </div>
                                </div>
                            </div>
                        </div>

                         {/* Form Page */}
                        <div id="kt_notes_panel" className={showCover === true ? 'offcanvas offcanvas-right kt-color-panel p-5 offcanvas-on':'offcanvas offcanvas-right kt-color-panel p-5'}>
                            {'okay'}
                        </div>
                       
                    </div>
                </div>
            </div>
        </div>
       
    )
}




export default withCookies(RequestPage);

