import { Link } from 'react-router-dom';
import Logo from '../assets/images/misc/logo.png';


const PosCharge = (props) => {

    const client = (id) => {
       return id;
    }

    
    return (
        <div className="col-xl-3 col-lg-4 col-md-4">
           <div className="card card-custom gutter-b bg-white border-0">
               <div className="card-body">
                  <div className="shop-profile">
                     <div className="media">
                        <div className="w-100px h-100px d-flex justify-content-center align-items-center">
                           <img src={Logo} height="100px"/>
                        </div>
                        <div className="media-body ml-3">
                           <b>
                              <h3 className="font-weight-bold">KUUKUA STORES</h3>
                              <p className="phoonenumber"> (054) 043-8290</p>
                              <p className="adddress">Abura Market, Cape Coast</p>
                              <p className="countryname">Ghana</p>
                           </b>
                        </div>
                        
                     </div>
                  </div>

                  <tr className={props.salesId == '' ? "d-flex align-items-center justify-content-between btn-primary px-3":"d-flex align-items-center justify-content-between btn-danger px-3"}>
                     <th className="border-0 font-size-h5 mb-0 font-size-bold text-white">
                       SESSION
                     </th>
                     <td className="border-0 justify-content-end d-flex text-white font-size-base"> <b>{ props.salesId == '' ? 'NEW CLIENT SALE' : 'EDITTING CLIENT SALE' } </b></td>
                  </tr><br/>
                  <tr className={props.salesId == '' ? "d-flex align-items-center justify-content-between btn-primary px-3":"d-flex align-items-center justify-content-between btn-danger px-3"}>
                     <th className={props.salesId == '' ? 'border-0 font-size-h5 mb-0 font-size-bold text-warning' : 'border-0 font-size-h5 mb-0 font-size-bold text-white'}>
                       CLIENT: 
                     </th>
                     <td className="border-0 justify-content-end d-flex text-warning font-size-base"> <small><b>{ props.client.name && props.client.name !== '' ? props.client.name.toUpperCase() : 'WALK-IN-CUSTOMER' } </b></small></td>
                  </tr>
                           
                  <div className="resulttable-pos">
                     <table className="table right-table">
                        <tbody>
                           <tr className="d-flex align-items-center justify-content-between">
                              <th className="border-0 font-size-h5 mb-0 font-size-bold text-dark">
                                 TOTAL ITEMS
                              </th>
                              <td className="border-0 justify-content-end d-flex text-dark font-size-base"><h3>{props.cart.length}</h3></td>
                           </tr>
                           <tr className="d-flex align-items-center justify-content-between">
                              <th className="border-0 font-size-h5 mb-0 font-size-bold text-dark">
                                 SUB-TOTAL
                              </th>
                              <td className="border-0 justify-content-end d-flex text-dark font-size-base">GHC { props.cart.reduce((acc,ct) => (ct.price * ct.quantity) + acc, 0).toFixed(2)}</td>
                           </tr>
                           <tr className="d-flex align-items-center justify-content-between">
                              <th className="border-0 ">
                                 <div className="d-flex align-items-center font-size-h5 mb-0 font-size-bold text-dark">
                                    DISCOUNT
                                    {/*
                                    <span className="badge badge-secondary white rounded-circle ml-2" data-toggle="modal" data-target="#discountpop">
                                       <svg xmlns="http://www.w3.org/2000/svg" className="svg-sm" xmlnsXlink="http://www.w3.org/1999/xlink" version="1.1" id="Layer_31" x="0px" y="0px" width="512px" height="512px" viewBox="0 0 512 512" enableBackground="new 0 0 512 512" xmlSpace="preserve">
                                          <g>
                                             <rect x="234.362" y={128} width="43.263" height={256} />
                                             <rect x={128} y="234.375" width={256} height="43.25" />
                                          </g>
                                       </svg>
                                    </span>
                                    */}
                                 </div>
                              </th>
                              <td className="border-0 justify-content-end d-flex text-dark font-size-base">0.0</td>
                           </tr>
                           <tr className="d-flex align-items-center justify-content-between">
                              <th className="border-0 font-size-h5 mb-0 font-size-bold text-dark">
                                 VAT
                              </th>
                              <td className="border-0 justify-content-end d-flex text-dark font-size-base"> { props.cart.length > 0 ? '0.0' : '0.0' } </td>
                           </tr>
                           <tr className="d-flex align-items-center justify-content-between item-price">
                              <th className="border-0 font-size-h5 mb-0 font-size-bold text-primary">
                                 TOTAL
                              </th>
                              <td className="border-0 justify-content-end d-flex text-primary font-size-base"><h4>GHC { props.cart.reduce((acc,ct) => (ct.price * ct.quantity) + acc, 0).toFixed(2) }</h4></td>
                           </tr>
                        </tbody>
                     </table>
                  </div>
                  {/*
                  <div className="d-flex justify-content-end align-items-center flex-column buttons-cash">
                     <div> 
                        <a href="#" className="btn btn-dark white mb-2" data-toggle="modal" data-target="#payment-popup">
                        <i className="fas fa-money-bill-wave mr-2" />
                         <b>Pay With Cash</b>
                        </a>
                     </div>
                     <div>
                        <a href="#" className="btn btn-outline-dark ">
                        <i className="fas fa-credit-card mr-2" />
                         <b>Pay On Credit</b>
                        </a>
                     </div>
                  </div>
                  */}

                  <div className="col-md-12 btn-submit d-flex justify-content-center">
                     <button type="submit" className="btn btn-danger mr-2  py-2 px-3" title="Delete" onClick={(e) => props.clearOrder(e)}>
                        <i className="fas fa-times mr-2" />
                        <b>CLEAR CART</b>
                     </button>
                     {/*
                     <button type="submit" className="btn btn-secondary white" onClick={() => props.saveOrder()}>
                        <svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" className="bi bi-folder-fill svg-sm mr-2" viewBox="0 0 16 16">
                           <path d="M9.828 3h3.982a2 2 0 0 1 1.992 2.181l-.637 7A2 2 0 0 1 13.174 14H2.826a2 2 0 0 1-1.991-1.819l-.637-7a1.99 1.99 0 0 1 .342-1.31L.5 3a2 2 0 0 1 2-2h3.672a2 2 0 0 1 1.414.586l.828.828A2 2 0 0 0 9.828 3zm-8.322.12C1.72 3.042 1.95 3 2.19 3h5.396l-.707-.707A1 1 0 0 0 6.172 2H2.5a1 1 0 0 0-1 .981l.006.139z" />
                        </svg>
                        <b>SAVE</b>
                     </button>
                     */}
                  </div>

               </div>
            </div>
        </div>
    )
}

export default PosCharge
