const PosFooter = (props) => { 

    return (
      <div id="kt_notes_panel" className={props.showSlider ? 'offcanvas offcanvas-right kt-color-panel p-5 offcanvas-on':'offcanvas offcanvas-right kt-color-panel p-5'}>
	     {<props.content {...props} ref={props.printRef} />}
      </div>
    )
}

export default PosFooter
