import { Link, useParams } from 'react-router-dom';
import { useState, useEffect } from 'react';
import { withCookies } from "react-cookie";
import Loader from '../assets/images/loadergif.gif';
import InvoiceCSS from '../components/InvoiceCss';
import { useSelector, useDispatch } from 'react-redux';


//import {axios} from 'axios';

const ProductHistoryPage = ({ cookies }) => {
    
    const [ title, setTitle ] = useState('Product History');
    const [ history, setHistory ] = useState([]);
    const [ filteredHistory, setFilteredHistory ] = useState([]);
    const [ showForm, setShowForm ] = useState(false);
    const [ formData, setFormData ] = useState({});
    const { REACT_APP_API_URL } = process.env;
    var user = cookies.get('user');
    const { productId } = useParams();
    
    // Initialize
    useEffect(()=>{
        const getHistory = async () => {
            const historyFromServer = await fetchHistory();
            if(historyFromServer.success){
                setTitle(historyFromServer.data[0].product.title+'  -- ( NEW STOCK QUANTITY : '+historyFromServer.data[0].product.quantity+' )')
                setHistory(historyFromServer.data);
                setFilteredHistory(historyFromServer.data);
            }
        }
        getHistory(); 
       
    },[]);

    // Keyword Search
    const keywordSearch = (e) => {
        var pros = history.filter( hist => hist.created_at.trim().toLowerCase().includes(e.target.value.trim().toLowerCase()) || hist.order.oid.toString().includes(e.target.value.trim().toLowerCase()) || hist.order.refname.trim().toLowerCase().includes(e.target.value.trim().toLowerCase()));
        setFilteredHistory(pros);
    }
  
    // Fetch Products
    const fetchHistory = async () => {
       const res = await fetch(`${REACT_APP_API_URL}/producthistory/${productId}`,{ mode:'cors' })
       const data = await res.json();
       return data;
    }

   
    const printProducts = () => {
        var content = document.getElementById("print");
        var pri = document.getElementById("printer");
        const style = document.createElement("style");
        style.innerHTML = `

            table,tr,td{
                border-collapse: collapse;
            }
            table{
                border:1px solid #999;
                padding: 5px;
                font-family: Courir, helvetica, sans-serif;
                font-size: 11px;
                
            }

            thead th{
                background: #eee;
                padding: 5px 9px;
                font-size:12px;
                text-align:center;
            }

            tbody td{
                border:thin solid #999;
                padding: 5px 10px;
            }

            .no-print{
                display:none;
            }

            .no-screen{
                display:block;
            }
        `;
        pri.contentWindow.document.open();
        pri.contentWindow.document.write(content.innerHTML);//1
        pri.contentDocument.head.appendChild(style)
        pri.contentWindow.document.close();
        pri.contentWindow.focus();
        pri.contentWindow.print();
    }
    

    /* PAGES */

   
    const ListPage = () => (
        <div id="print">
            <h3 className="no-screen">{title.toUpperCase()} || SALES HISTORY : {filteredHistory && filteredHistory.length}</h3>
            <div className="table-responsive" id="printableTable">
            <table id="productUnitTable" className="display dataTable no-footer" role="grid" aria-describedby="productUnitTable_info">
                <thead className="text-body">
                    <tr role="row">
                        <th>NO.</th>
                        <th>SALES DATE</th>
                        <th>QUANTITY SOLD</th>
                        <th>UNIT PRICE (GHC)</th>
                        <th>TOTAL COST (GHC)</th>
                        <th>SALES RECEIPT</th>
                        <th>CUSTOMER/CLIENT</th>
                        <th className="no-print">ACTION</th>
                    </tr>
                </thead>
                <tbody className="kt-table-tbody text-dark">
                    {filteredHistory.map((row,index) => (
                        <tr className="kt-table-row kt-table-row-level-0 odd" role="row" key={row._id}>
                            <td><b>#{index+1}</b></td>
                            <td><b>{row.created_at && row.created_at.toUpperCase()}</b></td>
                            <td><b>{row.quantity}</b></td>
                            <td><b>GHC {row.price}</b></td>
                            <td><b>GHC {row.amount.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</b></td>
                            <td><b><em>#{row.order.oid}</em></b></td>
                            <td><b>{row.order.refname && row.order.refname.toUpperCase()}</b></td>
                            <td className="no-print"></td>
                        </tr>
                    ))}
                    
                    </tbody>
                </table>
            </div>
            <iframe id="printer" style={{height: '0px', width: '0px', position: 'absolute'}}></iframe>
        </div>
    );


    return (
        <div className="d-flex flex-column-fluid pt-5">
            <div className="container-fluid pt-5">
                <div className="row">
                    <div className="col-12">
                        <div className="row">
                            <div className="col-lg-12 col-xl-12">
                                <div className="card card-custom gutter-b bg-transparent shadow-none border-0">
                                <div className="card-header align-items-center  border-bottom-dark px-0">
                                 
                                 {/* Page Title */}
                                <div className="card-title mb-0">
                                    <h3 className="card-label mb-0 font-weight-bold text-body">PRODUCTS HISTORY : {title.toUpperCase()}</h3>
                                </div>

                                 {/* Actions */}
                                <div className="icons d-flex">
                                    <Link to="#"  className="ml-2" onClick={()=> printProducts()}>
                                        <span className="icon h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle ">
                                            <svg width="15px" height="15px" viewBox="0 0 16 16" className="bi bi-printer-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                            <path d="M5 1a2 2 0 0 0-2 2v1h10V3a2 2 0 0 0-2-2H5z" />
                                            <path fillRule="evenodd" d="M11 9H5a1 1 0 0 0-1 1v3a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1v-3a1 1 0 0 0-1-1z" />
                                            <path fillRule="evenodd" d="M0 7a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v3a2 2 0 0 1-2 2h-1v-2a2 2 0 0 0-2-2H5a2 2 0 0 0-2 2v2H2a2 2 0 0 1-2-2V7zm2.5 1a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z" />
                                            </svg>
                                        </span>
                                    </Link>
                                {/* 
                                    <Link to="#" className="ml-2">
                                        <span className="icon h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle ">
                                            <svg width="15px" height="15px" viewBox="0 0 16 16" className="bi bi-file-earmark-text-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                            <path fillRule="evenodd" d="M2 2a2 2 0 0 1 2-2h5.293A1 1 0 0 1 10 .293L13.707 4a1 1 0 0 1 .293.707V14a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V2zm7 2l.5-2.5 3 3L10 5a1 1 0 0 1-1-1zM4.5 8a.5.5 0 0 0 0 1h7a.5.5 0 0 0 0-1h-7zM4 10.5a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5zm0 2a.5.5 0 0 1 .5-.5h4a.5.5 0 0 1 0 1h-4a.5.5 0 0 1-.5-.5z" />
                                            </svg>
                                        </span>
                                    </Link>
                                */}
                                </div>
                                </div>
                            </div>
                            </div>
                        </div>

                        {/* List Page */}
                        <div className="row">
                            <div className="col-12 ">
                                <div className="card card-custom gutter-b bg-white border-0">
                                    <div class="card-body">
                                        <div class="form-group row mb-0">
                                            <div class="col-md-12">
                                                <label><b>PRODUCT SALES BOOK</b></label>
                                                <fieldset class="form-group mb-0 d-flex barcodeselection">
                                                    <input type="text" className="form-control border-dark" onChange={(e)=> keywordSearch(e)} placeholder="SEARCH SALES ..."/>
                                                </fieldset>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="card-body">
                                       { ListPage() }
                                    </div>
                                </div>
                            </div>
                        </div>

                         {/* Form Page */}
                        <div id="kt_notes_panel" className={showForm === true ? 'offcanvas offcanvas-right kt-color-panel p-5 offcanvas-on':'offcanvas offcanvas-right kt-color-panel p-5'}>
                           TESTING
                        </div>
                       
                    </div>
                </div>
            </div>
        </div>
       
    )
}




export default withCookies(ProductHistoryPage);

