import { useEffect, useState, useContext } from 'react';
import { BrowserRouter as Router, Route,Switch,useHistory,Redirect } from 'react-router-dom';
import { withCookies } from "react-cookie";
import AdminPage from './pages/AdminPage';
import DashPage from './pages/DashPage';
import PosPage from './pages/PosPage';
import ProductPage from './pages/ProductPage';
import ProductHistoryPage from './pages/ProductHistoryPage';
import DraftOrderPage from './pages/DraftOrderPage';
import CashSalePage from './pages/CashSalePage';
import CompleteSalePage from './pages/CompleteSalePage';
import CreditSalePage from './pages/CreditSalePage';
import RejectSalePage from './pages/RejectSalePage';
import PaymentPage from './pages/PaymentPage';
import CatPage from './pages/CatPage';
import VatPage from './pages/VatPage';
import OrderPage from './pages/OrderPage';
import CustomerPage from './pages/CustomerPage';
import TransactionPage from './pages/TransactionPage';
import ReportPage from './pages/ReportPage';
import UserPage from './pages/UserPage';
import Login from './pages/Login';
import SalesAccountPage from './pages/SalesAccountPage';
import RestockPage from './pages/RestockPage';
import RequestPage from './pages/RequestPage';
import DailySalePage from './pages/DailySalePage';
import SalesReportPage from './pages/SalesReportPage';
import LogPage from './pages/LogPage';
import { useSelector, useDispatch } from 'react-redux';
import { SocketContext, socket } from './context/socket';
import LogStockPage from './pages/LogStockPage';
import LogStockViewPage from './pages/LogStockViewPage';
import StockAlertPage from './pages/StockAlertPage';


function App({cookies}) {
  
  const { REACT_APP_API_URL } = process.env;
  const history = useHistory();
  const state = useSelector(state => state)
  
  const logout = () => {
     var ok = window.confirm('Logout of System ?');
     if(ok){ cookies.remove('user') }
     // Dispatch logout from authReducer
  }

  let PrivateRoute = ({ component: Component, cookies, menu, ...rest }) => (
      <Route
          {...rest}
          render={props =>
            cookies.get('user') ? (
              <AdminPage user={cookies.get('user')} menu={menu} logout={logout} page={<Component {...props} />}/>
            ) : (
              <Redirect
                to={{
                  pathname: "/login",
                  state: { from: props.location }
                }}
              />
            )
          }
      />
  );  PrivateRoute = withCookies(PrivateRoute);

  let POSPrivateRoute = ({ component: Component, cookies, ...rest}) => (
    <Route
        {...rest}
        render={props =>
          cookies.get('user') ? (
            <Component {...props} user={cookies.get('user')} menu="pos" logout={logout}  />
          ) : (
            <Redirect
              to={{
                pathname: "/login",
                state: { from: props.location }
              }}
            />
          )
        }
    />
);  POSPrivateRoute = withCookies(POSPrivateRoute);

  return (
    <SocketContext.Provider value={socket}>
    <Router>
      <Switch>
        <Route exact path="/" component={Login}/>
        <Route path="/login" component={Login}/>
        <PrivateRoute path="/dash" component={DashPage} menu="dash"  />
        <PrivateRoute path="/products" component={ProductPage} menu="products"  />
        <PrivateRoute path="/producthistory/:productId" component={ProductHistoryPage} menu="products"  />
        <PrivateRoute path="/categories" component={CatPage} menu="categories"  />
        <PrivateRoute path="/vat" component={VatPage} menu="vat"  />
        <PrivateRoute path="/drafts" component={DraftOrderPage} menu="drafts"  />
        <PrivateRoute path="/customers" component={CustomerPage} menu="customers"  />
        <PrivateRoute path="/orders" component={OrderPage} menu="orders"  />
        <PrivateRoute path="/customers" component={CustomerPage} menu="customers"  />
        <PrivateRoute path="/reports" component={ReportPage} menu="reports"  />
        <PrivateRoute path="/alerts" component={StockAlertPage} menu="alerts"  />
        <PrivateRoute path="/users" component={UserPage} menu="users"  />
        <POSPrivateRoute path="/pos" component={PosPage} />
        <PrivateRoute path="/transactions" component={TransactionPage} menu="transactions"  />
        <PrivateRoute path="/cashsales" component={CashSalePage} menu="cashsales"  />
        <PrivateRoute path="/creditsales" component={CreditSalePage} menu="creditsales"  />
        <PrivateRoute path="/completesales" component={CompleteSalePage} menu="completesales"  />
        <PrivateRoute path="/rejectsales" component={RejectSalePage} menu="rejectsales"  />
        <PrivateRoute path="/payments" component={PaymentPage} menu="payments"  />
        <PrivateRoute path="/selling" component={SalesAccountPage} menu="selling"  />
        <PrivateRoute path="/stocking" component={RestockPage} menu="stocking"  />
        <PrivateRoute path="/requests" component={RequestPage} menu="requests"  />
        <PrivateRoute path="/dailysales" component={DailySalePage} menu="dailysales"  />
        <PrivateRoute path="/salesreport" component={SalesReportPage} menu="salesreport"  />
        <PrivateRoute path="/stocklogs" component={LogStockPage} menu="stocklogs"  />
        <PrivateRoute path="/stocklogview" component={LogStockViewPage} menu="stocklogview"  />
        <PrivateRoute path="/logs" component={LogPage} menu="logs"  />
      </Switch>
    </Router>
    </SocketContext.Provider>
  
    
  );
}

export default withCookies(App);
