import { Link } from 'react-router-dom';
import { useEffect, useState, useRef } from 'react';
import moment from 'moment';
import OnlineImage from '../assets/images/online2.jpg';
import OfflineImage from '../assets/images/offline2.jpg';

const PosHeader = ({ sessions,user,logout,resumeSession,orders, updateOrder, isOnline}) => { 

	 const [ timer, setTimer ] = useState({d:'',h:'',m:'',a:''});
    
	 // Util Functions
	 const useInterval = (callback, delay) => {
		const savedCallback = useRef();
		useEffect(() => {
		savedCallback.current = callback;
		}, [callback]);
		useEffect(() => {
		   function tick() {
			  savedCallback.current();
		   }
		   if (delay !== null) {
			  let id = setInterval(tick, delay);
			  return () => clearInterval(id);
		   }
		}, [delay]);
	 }
	 
	// Current Timer or Clock
	useInterval(() => {
	   const time = {d:moment().format("ddd MMM DD, YYYY").toUpperCase(), h:moment().format("hh"),m:moment().format("mm"),a:moment().format("A")};
	   setTimer(time);
	},1000);
    
    return (
        <header className="pos-header bg-white">
				<div className="container-fluid">
					<div className="row align-items-center">
						<div className="col-xl-4 col-lg-4 col-md-6">
							<div className="greeting-text">
								<h3 className="card-label mb-0 font-weight-bold text-dark">POS CLIENT</h3>
								<h3 className="card-label mb-0 ">{ user && user.name.toUpperCase() }</h3>
							</div>
						</div>
						<div className="col-xl-4 col-lg-5 col-md-6  clock-main">
							<div className="clock">
								
								<div className="datetime-content">
									<ul>
										<li id="hours"><b>{timer.h}</b></li>
										<li id="point">:</li>
										<li id="min"><b>{timer.m}</b></li>
										<li id="point1">&nbsp;&nbsp;&nbsp;</li>
										<li id="sec"><small><b>{timer.a}</b></small></li>
			   			 			</ul>
								</div>
								{/**/}
								<div className="datetime-conte nt">
									<div id="Date" className><b>{timer.d}</b></div>
								</div>
							</div>
						</div>
						<div className="col-xl-4 col-lg-3 col-md-12  order-lg-last order-second">
							<div className="topbar justify-content-end">
						     	
								 <Link to={user && user.role == 'admin' ? '/dash':'/selling'} className="topbar-item">
									<div className="btn btn-icon  w-auto h-auto btn-clean d-flex align-items-center py-0 mr-3" data-toggle="modal" data-target="#folderpop">
										<span className="badge badge-pill badge-warning">5</span>
										<span className="symbol symbol-35  symbol-light-success">
											<span className="symbol-label bg-dark font-size-h5 ">
											<i className="white fa fa-lg fa-home"></i>
											</span>
										</span>
									</div>
								</Link>
                                
                                
								
								<div className="dropdown">
									<div className="topbar-item" data-toggle="dropdown" data-display="static">
										<div className="btn btn-icon w-auto h-auto btn-clean d-flex align-items-center py-0">
											<span className="symbol symbol-35 symbol-light-success">
											<span className="symbol-label font-size-h5 ">
												<svg width="20px" height="20px" viewBox="0 0 16 16" className="bi bi-person-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
													<path fillRule="evenodd" d="M3 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H3zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z" />
												</svg>
											</span>
											</span>
										</div>
									</div>
									<div className="dropdown-menu dropdown-menu-right" style={{minWidth: '150px'}}>
									<Link to="#" onClick={logout}  className="dropdown-item">
										<span className="svg-icon svg-icon-xl svg-icon-primary mr-2">
											<svg xmlns="http://www.w3.org/2000/svg" width="20px" height="20px" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth={2} strokeLinecap="round" strokeLinejoin="round" className="feather feather-power">
											<path d="M18.36 6.64a9 9 0 1 1-12.73 0" />
											<line x1={12} y1={2} x2={12} y2={12} />
											</svg>
										</span>
										Logout
									</Link>
								   </div>
							    </div>

                                <img src={isOnline ? OnlineImage : OfflineImage } alt="Connecton" style={{ height: '30px', marginLeft: '15px'}} />


								<div className="dropdown ml-3">
									<div className="topbar-item" data-toggle="dropdown" data-display="static">
									    <div className="btn btn-dark w-auto h-auto d-flex align-items-center py-2">
										   <b className="badge badge-pill badge-warning">{orders.length}</b>&nbsp;&nbsp;
										   <small><b className="symbol symbol-35 symbol-light-success">SALES</b></small>
										</div>
									</div>
									<div className="dropdown-menu dropdown-menu-right" style={{minWidth: '150px'}}>
									{
										orders ? orders.map((order,index) => (
											<Link to="#" key={index} onClick={()=> updateOrder(order._id)}  className="dropdown-item">
												<b>{index+1}.&nbsp;&nbsp;OID:&nbsp;&nbsp;#{order.oid} - GHC {order.amount} </b>
											</Link>
										)):null
                                    }
								  </div>
							    </div>



								<div className="dropdown ml-3">
									<div className="topbar-item" data-toggle="dropdown" data-display="static">
										<div className="btn btn-warning w-auto h-auto d-flex align-items-center py-2">
										   <b className="badge badge-pill badge-dark">{sessions.length}</b>&nbsp;&nbsp;
										   <small><b className="symbol symbol-35 symbol-light-success">SAVED</b></small>
										</div>
									</div>
									<div className="dropdown-menu dropdown-menu-right" style={{minWidth: '150px'}}>
									{
										sessions ? sessions.map((session,index) => (
											<Link to="#" key={index} onClick={()=> resumeSession(index)}  className="dropdown-item">
												<b><i className="fas fa-user"></i>&nbsp;&nbsp;CLIENT #{index+1}</b>
												<small><em><b>&nbsp;&nbsp;-- {session.tag} </b></em></small>
											</Link>
										)):null
                                    }
								  </div>
							    </div>



						</div>
					</div>
				</div>
			</div>
		</header>
    )
}

export default PosHeader
