import { Link } from 'react-router-dom';
import { useState, useEffect} from 'react';
import Loader from '../assets/images/loadergif.gif';
//import {axios} from 'axios';

const LogPage = () => {

    const [ logs, setLogs ] = useState([]);
    const [ filteredLogs, setFilteredLogs ] = useState([]);
    const { REACT_APP_API_URL } = process.env;

    // Initialize
    useEffect(()=>{
        const getLogs = async () => {
            const logsFromServer = await fetchLogs();
           
            if(logsFromServer.success){
                var hdata = [...logsFromServer.data];
                setLogs(hdata);
                setFilteredLogs(hdata);
            }
        }
        getLogs(); 
    },[]);

    // Keyword Search
    const keywordSearch = (e) => {
        var pros = logs.filter( log => log.action.toLowerCase().includes(e.target.value.trim().toLowerCase()) || log.user.toLowerCase().includes(e.target.value.trim().toLowerCase()));
        setFilteredLogs(pros);
    }
  

    // Fetch Users
    const fetchLogs = async () => {
       const res = await fetch(`${REACT_APP_API_URL}/logs`,{ mode:'cors' })
       const data = await res.json();
       return data;
    }

    /* PAGES */

   
    const ListPage = () => (
        <div>
            <div className="table-responsive" id="printableTable">
            <table id="productUnitTable" className="display dataTable no-footer" role="grid" aria-describedby="productUnitTable_info">
                <thead className="text-body">
                    <tr role="row">
                        <th>ACTION</th>
                        <th>TIME</th>
                        <th>USER</th>
                    </tr>
                </thead>
                <tbody className="kt-table-tbody text-dark">
                    {filteredLogs.map((log,index) => (
                        <tr className="kt-table-row kt-table-row-level-0 odd" role="row" key={log._id}>
                            <td><b>{log.action ? log.action.toUpperCase() : 'NONE'}</b></td>
                            <td><em>{log.timestamp}</em></td>
                            <td>{log.user}</td>
                        </tr>
                    ))}
                    
                    </tbody>
                </table>
            </div>
        </div>
    );


    return (
        <div className="d-flex flex-column-fluid pt-5">
            <div className="container-fluid pt-5">
                <div className="row">
                    <div className="col-12">
                        <div className="row">
                            <div className="col-lg-12 col-xl-12">
                                <div className="card card-custom gutter-b bg-transparent shadow-none border-0">
                                <div className="card-header align-items-center  border-bottom-dark px-0">
                                 
                                 {/* Page Title */}
                                <div className="card-title mb-0">
                                    <h3 className="card-label mb-0 font-weight-bold text-body">SYSTEM LOGS</h3>
                                </div>

                                 {/* Actions */}
                                 <div className="icons d-flex">
                                     {/*
                                    <button className="btn ml-2 p-0"  onClick={() => null }>
                                        <span className="bg-dark h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle shadow-sm ">
                                            <i className="fa fa-plus text-white"></i> 
                                        </span>
                                    </button>
                                     */}
                                    <Link to="#"  className="ml-2">
                                        <span className="icon h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle ">
                                            <svg width="15px" height="15px" viewBox="0 0 16 16" className="bi bi-printer-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                            <path d="M5 1a2 2 0 0 0-2 2v1h10V3a2 2 0 0 0-2-2H5z" />
                                            <path fillRule="evenodd" d="M11 9H5a1 1 0 0 0-1 1v3a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1v-3a1 1 0 0 0-1-1z" />
                                            <path fillRule="evenodd" d="M0 7a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v3a2 2 0 0 1-2 2h-1v-2a2 2 0 0 0-2-2H5a2 2 0 0 0-2 2v2H2a2 2 0 0 1-2-2V7zm2.5 1a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z" />
                                            </svg>
                                        </span>
                                    </Link>
                                    {/* 
                                    <Link to="#" className="ml-2">
                                        <span className="icon h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle ">
                                            <svg width="15px" height="15px" viewBox="0 0 16 16" className="bi bi-file-earmark-text-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                            <path fillRule="evenodd" d="M2 2a2 2 0 0 1 2-2h5.293A1 1 0 0 1 10 .293L13.707 4a1 1 0 0 1 .293.707V14a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V2zm7 2l.5-2.5 3 3L10 5a1 1 0 0 1-1-1zM4.5 8a.5.5 0 0 0 0 1h7a.5.5 0 0 0 0-1h-7zM4 10.5a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5zm0 2a.5.5 0 0 1 .5-.5h4a.5.5 0 0 1 0 1h-4a.5.5 0 0 1-.5-.5z" />
                                            </svg>
                                        </span>
                                    </Link>
                                */}
                                </div>
                                </div>
                            </div>
                            </div>
                        </div>

                        {/* List Page */}
                        <div className="row">
                            <div className="col-12 ">
                                <div className="card card-custom gutter-b bg-white border-0">
                                    <div class="card-body">
                                        <div class="form-group row mb-0">
                                            <div class="col-md-12">
                                                <label><b>LOGS</b></label>
                                                <fieldset class="form-group mb-0 d-flex barcodeselection">
                                                    <input type="text" className="form-control border-dark" onChange={(e)=> keywordSearch(e)} placeholder="SEARCH LOGS ..."/>
                                                </fieldset>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="card-body">
                                       { ListPage() }
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    )
}




export default LogPage

