import { Link } from 'react-router-dom';
import ProductImage from '../assets/images/element-banner2-right.jpeg';
import Cement from '../assets/images/cement.png';
import POS from '../assets/images/pos.jpg';

const PosProduct = (props) => {
    
    return (
       
      <div className="col-xl-4 order-xl-first order-last">
      <div className="card card-custom gutter-b bg-white border-0">
         <div className="card-body">
            <div className="form-group row mb-0">
               <div className="col-md-12">
                  <label><b>PRODUCTS CATALOGUE</b></label>
                  <fieldset className="form-group mb-0 d-flex barcodeselection">
                     <select className="form-control w-50" id="exampleFormControlSelect1" onChange={(e) => props.categorySearch(e)} >
                        <option value={123456}>ALL PRODUCTS</option>
                     </select>
                     <input type="text" className="form-control border-dark" onChange={(e) => props.keywordSearch(e)} placeholder="SEARCH PRODUCTS ..." />
                  </fieldset>
               </div>
            </div>
         </div><hr/>
         <div className="product-items">
           <div className="row">
             { props.products.map(product => (
               <div className="col-xl-4 col-lg-2 col-md-3 col-sm-4 col-6" key={product._id}>
                  <div className="productCard" onClick={() => props.addToCart(product._id)}>
                     <div className="productThumb">
                        <img className="img-fluid" src={POS} alt="ix" />
                     </div>
                     <div className="productContent">
                        <h6>{product.title.toUpperCase()}</h6>
                        <small><b><em>[GHC {product.price}]</em></b></small><br/>
                        <small><b><em className="text-primary">QTY: {product.quantity}</em></b></small>
                     </div>
                  </div>
               </div>
              ))
             }
              
            </div>
         </div>
      </div>
   </div>

    )
}

export default PosProduct
