import { Link } from 'react-router-dom';
import { useState, useEffect, useCallback, useContext } from 'react';
import { withCookies } from "react-cookie";
import Loader from '../assets/images/loadergif.gif';
import InvoiceCSS from '../components/InvoiceCss';
import { useSelector, useDispatch } from 'react-redux';
import { SocketContext} from '../context/socket';
import { setProducts } from '../context/shopSlice';

//import {axios} from 'axios';

const StockAlertPage = ({ cookies }) => {

    const [ produx, setProdux ] = useState([]);
    const [ filteredProducts, setFilteredProducts ] = useState([]);
    const [ helpers, setHelpers ] = useState({});
    const [ showForm, setShowForm ] = useState(false);
    const [ showLoader, setShowLoader ] = useState(true);
    const [ formState, setFormState ] = useState('add');
    const [ formData, setFormData ] = useState({});
   
    const socket = useContext(SocketContext);
    const { REACT_APP_API_URL } = process.env;
    var user = cookies.get('user');
    const  dispatch = useDispatch();
    const { products } = useSelector(state => state.shop);
    console.log(products);
    const getProducts = async () => {
        // Fetch Updates from Server
        const productsFromServer = await fetchProducts();
        if(productsFromServer.success){
           //dispatch(setProducts([...productsFromServer.data]))
           setProdux([...productsFromServer.data])
           setFilteredProducts([...productsFromServer.data])
        }  setShowLoader(false);
    }

    const getHelpers = async () => {
      // Fetch Updates from Server
      const helpersFromServer = await fetchHelpers();
      if(helpersFromServer.success){
        setHelpers({...helpersFromServer.data});
      }
    }
   
    // Load from Store
    useEffect(() => {
      setProdux([...products]);
      setFilteredProducts([...products]);
      getProducts(); 
      getHelpers();
    },[]);

    /*
    useEffect(() => {
      dispatch(setProducts([...produx]))
    },[produx]);
    */
   
     
    // Keyword Search
    const keywordSearch = (e) => {
        var pros = produx.filter( product => product.title.trim().toLowerCase().includes(e.target.value.trim().toLowerCase()));
        setFilteredProducts(pros);
    }
  
    // Fetch Products
    const fetchProducts = async () => {
       const res = await fetch(`${REACT_APP_API_URL}/products`,{ mode:'cors' })
       const data = await res.json();
       return data;
    }

    const fetchHelpers = async () => {
        const res = await fetch(`${REACT_APP_API_URL}/helpers`,{ mode:'cors' })
        const data = await res.json();
        return data;
    }

    
    // Input handler
    const onChange = (e) => setFormData({ ...formData, [e.target.name]: e.target.value });

   
    const printProducts = () => {
        var content = document.getElementById("print");
        var pri = document.getElementById("printer");
        const style = document.createElement("style");
        style.innerHTML = `

            table,tr,td{
                border-collapse: collapse;
            }
            table{
                border:1px solid #999;
                padding: 5px;
                font-family: Courir, helvetica, sans-serif;
                font-size: 11px;
                
            }

            thead th{
                background: #eee;
                padding: 5px 9px;
                font-size:12px;
                text-align:center;
            }

            tbody td{
                border:thin solid #999;
                padding: 5px 10px;
            }

            .no-print{
                display:none;
            }

            .no-screen{
                display:block;
            }
        `;
        pri.contentWindow.document.open();
        pri.contentWindow.document.write(content.innerHTML);//1
        pri.contentDocument.head.appendChild(style)
        pri.contentWindow.document.close();
        pri.contentWindow.focus();
        pri.contentWindow.print();
    }

    
    /* PAGES */
    const ListPage = () => (
        <div id="print">
            <h1 className="no-screen">STOCK LEVELS ({filteredProducts && filteredProducts.length})</h1>
            <div className="table-responsive" id="printableTable">
            <table id="productUnitTable" className="display dataTable no-footer" role="grid" aria-describedby="productUnitTable_info">
                <thead className="text-body">
                    <tr role="row">
                        <th>NO.</th>
                        <th>PRODUCT</th>
                        <th>SELLING PRICE</th>
                        <th>QUANTITY</th>
                       <th className="no-print">ACTION</th>
                    </tr>
                </thead>
                <tbody className="kt-table-tbody text-dark">
                    {filteredProducts.map((product,index) => (
                        <tr className="kt-table-row kt-table-row-level-0 odd" role="row" key={product._id} style={product.quantity <= 0 ? { color:'red'}: ((product.quantity <= product.min_quantity && product.quantity > 0) ? {'color':'#cb7f0e'}:{'color':'inherit'})}>
                            <td><b>#{index+1}</b></td>
                            <td><b>{product.title.toUpperCase()}</b></td>
                            <td><b>GHC {product.price}</b></td>
                            <td><b>{product.quantity <= 0 ? 'OUT OF STOCK' : product.quantity}</b></td>
                            {/*<td>{product.Vat && product.Vat}</td>*/}
                            <td className="no-print"><b className="mr-0 text-dark">{product.quantity <= 0 ? 'OUT OF STOCK':'IN STOCK'}</b></td>
                        </tr>
                    ))}
                    
                    </tbody>
                </table>
            </div>
            <iframe id="printer" style={{display:'none'}}></iframe>
        </div>
    );

   

    return (
        <div className="d-flex flex-column-fluid pt-5">
            <div className="container-fluid pt-5">
                <div className="row">
                    <div className="col-12">
                        <div className="row">
                            <div className="col-lg-12 col-xl-12">
                                <div className="card card-custom gutter-b bg-transparent shadow-none border-0">
                                <div className="card-header align-items-center  border-bottom-dark px-0">
                                 
                                 {/* Page Title */}
                                <div className="card-title mb-0">
                                    <h3 className="card-label mb-0 font-weight-bold text-body">STOCK ALERTS</h3>
                                </div>

                                 {/* Actions */}
                                  <div className="icons d-flex">
                                    <Link to="#"  className="ml-2" onClick={()=> printProducts()}>
                                      <span className="icon h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle ">
                                        <svg width="15px" height="15px" viewBox="0 0 16 16" className="bi bi-printer-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M5 1a2 2 0 0 0-2 2v1h10V3a2 2 0 0 0-2-2H5z" />
                                        <path fillRule="evenodd" d="M11 9H5a1 1 0 0 0-1 1v3a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1v-3a1 1 0 0 0-1-1z" />
                                        <path fillRule="evenodd" d="M0 7a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v3a2 2 0 0 1-2 2h-1v-2a2 2 0 0 0-2-2H5a2 2 0 0 0-2 2v2H2a2 2 0 0 1-2-2V7zm2.5 1a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z" />
                                        </svg>
                                      </span>
                                    </Link>
                                 </div>
                                </div>
                            </div>
                            </div>
                        </div>

                        {/* List Page */}
                        <div className="row">
                            <div className="col-12 ">
                                <div className="card card-custom gutter-b bg-white border-0">
                                    <div class="card-body">
                                        <div class="form-group row mb-0">
                                            <div class="col-md-12">
                                                <label><b>STOCK ALERTS </b></label>
                                                <fieldset class="form-group mb-0 d-flex barcodeselection">
                                                    <input type="text" className="form-control border-dark" onChange={(e)=> keywordSearch(e)} placeholder="SEARCH STOCK ..."/>
                                                </fieldset>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="card-body">
                                       { ListPage() }
                                    </div>
                                </div>
                            </div>
                        </div>
   
                    </div>
                </div>
            </div>
        </div>
       
    )
}




export default withCookies(StockAlertPage);

