import { Link,useHistory } from 'react-router-dom';
import { withCookies, Cookies } from "react-cookie";

const Header = (props) => { 

	const { REACT_APP_API_URL } = process.env;
    var history = useHistory();

	// Reset User Password
    const resetUser = async () => {
		var id = props.user._id;
        var ins = window.prompt('Enter New Password');
        if(ins){
            var ins2 = window.prompt('Enter Re-peat password');
            if(ins === ins2){
                const res = await fetch(`${REACT_APP_API_URL}/users/reset/${id}?pwd=${ins}`, {
                    mode: 'cors',
                    method: 'GET',
                })
                const data = await res.json();
                if(data.success) alert("Reset success!");
                console.log(data);
				props.cookies.remove('user');
				history.push('/')
               
            }else{
              alert("Password mismatch");
            }
        }
    }


    return (
        <div id="tc_header" className="header header-fixed">
					<div className="container-fluid d-flex align-items-stretch justify-content-between">
						<div className="header-menu-wrapper header-menu-wrapper-left" id="tc_header_menu_wrapper">
							<div id="tc_header_menu" className="header-menu header-menu-mobile header-menu-layout-default">
								<ul className="menu-nav">

										<li className="menu-item menu-item-open menu-item-here menu-item-submenu menu-item-rel menu-item-open menu-item-here menu-item-active p-0"
										data-menu-toggle="click">
										<div className="btn  btn-clean btn-dropdown mr-0 p-0" id="tc_aside_toggle">
											<span className="svg-icon svg-icon-xl svg-icon-primary">
                                                <svg width="24px" height="24px" viewBox="0 0 16 16" className="bi bi-list"
													fill="currentColor" xmlns="http://www.w3.org/2000/svg">
													<path fill-rule="evenodd"
														d="M2.5 11.5A.5.5 0 0 1 3 11h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4A.5.5 0 0 1 3 7h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4A.5.5 0 0 1 3 3h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z" />
												</svg>
											</span>
										</div>
										
									</li>

								</ul>
								
							</div>
							
						</div>
						<div className="topbar">
						    { props.user && ['admin','sales'].includes(props.user.role) && 
							<div className="posicon d-lg-block d-none">
								<Link to="/pos" className="btn btn-primary white mr-2"><b>POS</b></Link>
							</div>
                           }
						
							
							<div className="dropdown">

								<div className="topbar-item" data-toggle="dropdown" data-display="static">
									<div className="btn btn-icon w-auto btn-clean d-flex align-items-center pr-1 pl-3">
										<b className="text-dark-50 font-size-base d-none d-xl-inline mr-3">{props.user && props.user.name.toUpperCase()}</b>
										<span className="symbol symbol-35 symbol-light-success">
											<span className="symbol-label font-size-h5 ">
												<svg width="20px" height="20px" viewBox="0 0 16 16"
													className="bi bi-person-fill" fill="currentColor"
													xmlns="http://www.w3.org/2000/svg">
													<path fill-rule="evenodd"
														d="M3 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H3zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z" />
												</svg>
											</span>
										</span>
									</div>
								</div>

								<div className="dropdown-menu dropdown-menu-right" style={{minWidth: 150}}>

									<Link to="#" className="dropdown-item">
										<span className="svg-icon svg-icon-xl svg-icon-primary mr-2">
											<svg xmlns="http://www.w3.org/2000/svg" width="20px" height="20px"
												viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"
												stroke-linecap="round" stroke-linejoin="round"
												className="feather feather-user">
												<path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2"></path>
												<circle cx="12" cy="7" r="4"></circle>
											</svg>
										</span>
										Edit Profile
									</Link>
									<Link to="#" className="dropdown-item" onClick={resetUser}>
										<i className="fas fa-lock fa-lg mr-3"></i>
										Reset Account
									</Link>

									<Link to="#" onClick={props.logout} className="dropdown-item">
										<span className="svg-icon svg-icon-xl svg-icon-primary mr-2">
											<svg xmlns="http://www.w3.org/2000/svg" width="20px" height="20px"
												viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"
												stroke-linecap="round" stroke-linejoin="round"
												className="feather feather-power">
												<path d="M18.36 6.64a9 9 0 1 1-12.73 0"></path>
												<line x1="12" y1="2" x2="12" y2="12"></line>
											</svg>
										</span>
										Logout
									</Link>
								</div>

							</div>
						</div>
					</div>
				</div>
    )
}

export default withCookies(Header)
