import { Link } from 'react-router-dom';
import { useState, useEffect, useCallback, useContext } from 'react';
import { withCookies } from "react-cookie";
import Loader from '../assets/images/loadergif.gif';
import InvoiceCSS from '../components/InvoiceCss';
import { useSelector, useDispatch } from 'react-redux';
import { SocketContext} from '../context/socket';
import { setProducts } from '../context/shopSlice';

//import {axios} from 'axios';

const ProductPage = ({ cookies }) => {

    const [ produx, setProdux ] = useState([]);
    const [ filteredProducts, setFilteredProducts ] = useState([]);
    const [ helpers, setHelpers ] = useState({});
    const [ showForm, setShowForm ] = useState(false);
    const [ showLoader, setShowLoader ] = useState(true);
    const [ formState, setFormState ] = useState('add');
    const [ formData, setFormData ] = useState({});
   
    const socket = useContext(SocketContext);
    const { REACT_APP_API_URL } = process.env;
    var user = cookies.get('user');
    const  dispatch = useDispatch();
    const { products } = useSelector(state => state.shop);
    console.log(products);
    const getProducts = async () => {
        // Fetch Updates from Server
        const productsFromServer = await fetchProducts();
        if(productsFromServer.success){
           //dispatch(setProducts([...productsFromServer.data]))
           setProdux([...productsFromServer.data])
           setFilteredProducts([...productsFromServer.data])
        }  setShowLoader(false);
    }

    const getHelpers = async () => {
      // Fetch Updates from Server
      const helpersFromServer = await fetchHelpers();
      if(helpersFromServer.success){
        setHelpers({...helpersFromServer.data});
      }
    }
   
    // Load from Store
    useEffect(() => {
      setProdux([...products]);
      setFilteredProducts([...products]);
      getProducts(); 
      getHelpers();
    },[]);

    /*
    useEffect(() => {
      dispatch(setProducts([...produx]))
    },[produx]);
    */
   
     
    // Keyword Search
    const keywordSearch = (e) => {
        var pros = produx.filter( product => product.title.trim().toLowerCase().includes(e.target.value.trim().toLowerCase()));
        setFilteredProducts(pros);
    }
  
    // Fetch Products
    const fetchProducts = async () => {
       const res = await fetch(`${REACT_APP_API_URL}/products`,{ mode:'cors' })
       const data = await res.json();
       return data;
    }

    const fetchHelpers = async () => {
        const res = await fetch(`${REACT_APP_API_URL}/helpers`,{ mode:'cors' })
        const data = await res.json();
        return data;
    }

    // Delete Product
    const deleteProduct = async (id)=>{
        var ok = window.confirm("Delete Product ? \n- This will affect Sales and Stock!");
        if(ok){
            const res = await fetch(`${REACT_APP_API_URL}/products/${id}`, {
                mode: 'cors',
                method: 'DELETE',
            })
            const data = await res.json();
            if(data.success){
              getProducts();
              socket.emit("LOAD_PRODUCTS"); // EMIT ON SOCKET NETWORK ON CHANGES!
            }
        }
    }


    // Edit Product
    const editProduct = async (id)=>{
        const res = await fetch(`${REACT_APP_API_URL}/products/${id}`, {mode: 'cors'})
        const data = await res.json();
        if(data.success){ setFormData(data.data); }
        setFormState('edit');
        setShowForm(true);
    }

    // Add Product
    const addProduct = () => {
        window.document.getElementById("myform").reset();
        setFormState('add');
        setShowForm(true);
        setFormData({Vat:helpers.vats[0]._id, Cat: helpers.categories[0]._id});
    }
    
    // Input handler
    const onChange = (e) => setFormData({ ...formData, [e.target.name]: e.target.value });

    // Save Product
    const onSubmit = async (e) => {
        e.preventDefault();
        setShowLoader(true);
        try{
           
           const body = JSON.stringify(formData);
           const url = `${REACT_APP_API_URL}/products${formState === 'add' ? "": "/"+formData._id}`;
           const method = formState === 'add' ? "POST": "PUT";
           const res = await fetch(url,{
               headers: {"Content-Type": "application/json"},
               mode:    "cors",
               method:  method,
               body:    body
           });
           const data = await res.json()
           if(data.success){
              getProducts(); 
              setShowForm(false);
              socket.emit("LOAD_PRODUCTS"); // EMIT ON SOCKET NETWORK ON CHANGES!
           }  setShowLoader(false);
        } catch (err) {
            setShowLoader(false);
        }
    };

    const printProducts = () => {
        var content = document.getElementById("print");
        var pri = document.getElementById("printer");
        const style = document.createElement("style");
        style.innerHTML = `

            table,tr,td{
                border-collapse: collapse;
            }
            table{
                border:1px solid #999;
                padding: 5px;
                font-family: Courir, helvetica, sans-serif;
                font-size: 11px;
                
            }

            thead th{
                background: #eee;
                padding: 5px 9px;
                font-size:12px;
                text-align:center;
            }

            tbody td{
                border:thin solid #999;
                padding: 5px 10px;
            }

            .no-print{
                display:none;
            }

            .no-screen{
                display:block;
            }
        `;
        pri.contentWindow.document.open();
        pri.contentWindow.document.write(content.innerHTML);//1
        pri.contentDocument.head.appendChild(style)
        pri.contentWindow.document.close();
        pri.contentWindow.focus();
        pri.contentWindow.print();
    }

    
    /* PAGES */
    const ListPage = () => (
        <div id="print">
            <h1 className="no-screen">STOCK LEVELS ({filteredProducts && filteredProducts.length})</h1>
            <div className="table-responsive" id="printableTable">
            <table id="productUnitTable" className="display dataTable no-footer" role="grid" aria-describedby="productUnitTable_info">
                <thead className="text-body">
                    <tr role="row">
                        <th>NO.</th>
                        <th>PRODUCT</th>
                        {/*<th>CATEGORY</th>*/}
                        <th>SKU</th>
                        <th>SELLING PRICE</th>
                        <th>COST PRICE</th>
                        <th>QUANTITY</th>
                        {/*<th>VAT RATE (%)</th>*/}
                        <th className="no-print">STATUS</th> 
                        <th className="no-print">ACTION</th>
                    </tr>
                </thead>
                <tbody className="kt-table-tbody text-dark">
                    {filteredProducts.map((product,index) => (
                        <tr className="kt-table-row kt-table-row-level-0 odd" role="row" key={product._id} style={product.quantity <= 0 ? { color:'red'}: (((product.quantity <= product.min_quantity && product.quantity > 0) || product.price <= 0) ? {'color':'#cb7f0e'}:{'color':'inherit'})}>
                            <td><b>#{index+1}</b></td>
                            <td><b>{product.title.toUpperCase()}</b></td>
                            {/*<td>{product.Cat ? product.Cat : 'None'}</td>*/}
                            <td><em>{product.sku}</em></td>
                            <td><b>{product.price <= 0 ? `NO PRICE SET`:`GHC ${product.price}`}</b></td>
                            <td>GHC {product.cprice}</td>
                            <td><b>{product.quantity <= 0 ? 'OUT OF STOCK' : product.quantity}</b></td>
                            {/*<td>{product.Vat && product.Vat}</td>*/}
                            <td className="no-print"><b className="mr-0 text-dark">{product.status === 1 ? 'ACTIVE':'INACTIVE'}</b></td>
                            <td className="no-print">

                              { user.role === 'admin' ?
                                <div className="card-toolbar text-right">
                                <button className="btn p-0 shadow-none" type="button" id="dropdowneditButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    <span className="svg-icon">
                                    <svg width="20px" height="20px" viewBox="0 0 16 16" className="bi bi-three-dots text-body" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                        <path fillRule="evenodd" d="M3 9.5a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3z" />
                                    </svg>
                                    </span>
                                </button>

                                <div className="dropdown-menu dropdown-menu-right" aria-labelledby="dropdowneditButton" style={{position: 'absolute', transform: 'translate3d(1001px, 111px, 0px)', top: '0px', left: '0px', willChange: 'transform'}}>
                                    <Link to={{ pathname:`/producthistory/${product._id}`}} className="dropdown-item" data-toggle="tooltip" title="Edit" data-placement="right">History</Link>
                                    <button className="dropdown-item"  onClick={() => editProduct(product._id)} data-toggle="tooltip" title="Edit" data-placement="right">Edit</button>
                                    <button className="dropdown-item" onClick={() => deleteProduct(product._id)} title="Delete" >Delete</button>
                                </div>
                               
                                </div> 
                              : (product.quantity <= 0 ? 'OUT OF STOCK':'IN STOCK') }

                            </td>
                        </tr>
                    ))}
                    
                    </tbody>
                </table>
            </div>
            <iframe id="printer" style={{display:'none'}}></iframe>
        </div>
    );

    const FormPage = () => (
        <>
            <div className="offcanvas-header d-flex align-items-center justify-content-between pb-3">
                <h4 className="font-size-h4 font-weight-bold m-0">{ formState === 'add' ? 'ADD PRODUCT':'EDIT PRODUCT'} <hr/></h4>
                <button className="btn btn-sm btn-icon btn-light btn-hover-primary p-2" onClick={() => setShowForm(false)}><i className="fa fa-times"></i></button>
            </div>

            <form id="myform" onSubmit={onSubmit} noValidate={true}>
              <div className="row">
                <div className="col-12">
                    <div className="form-group">
                        <label className="text-dark">Product Title</label>
                        <input type="text" name="title" value={formData.title}  onChange={onChange} className="form-control" />
                    </div>
                    <div className="form-group">
                        <label className="text-dark">Product Description</label>
                        <input type="text" name="description"  value={formData.description} onChange={onChange} className="form-control" />
                    </div>
                    <div className="form-group">
                        <label className="text-dark">Product Category</label>
                        <select className="arabic-select w-100 mb-3 h-30px" name="Cat" onChange={onChange} value={formData.Cat}>
                           {helpers.categories && helpers.categories.map(cat => (
                            <option value={cat._id}>{cat.title}</option>
                           ))}
                        </select>
                    </div>
                    <div className="form-group">
                        <label className="text-dark">VAT Rate</label>
                        <select className="arabic-select w-100 mb-3 h-30px" name="Vat" onChange={onChange} value={formData.Vat}>
                           {helpers.vats && helpers.vats.map(vat => (
                            <option value={vat._id}>{vat.rate}</option>
                           ))}
                        </select>
                    </div>
                    <div className="form-group">
                        <label className="text-dark">Product SKU</label>
                        <input type="text" name="sku"  value={formData.sku} onChange={onChange} readOnly={formState === 'edit' && user.role !== 'admin'} className="form-control" />
                    </div>
                    <div className="form-group">
                        <label className="text-dark">Selling Price (GHC)</label>
                        <input type="text" name="price"  value={formData.price} onChange={onChange} className="form-control" />
                    </div>
                    <div className="form-group">
                        <label className="text-dark">Cost Price (GHC)</label>
                        <input type="text" name="cprice"  value={formData.cprice} onChange={onChange} className="form-control" />
                    </div>

                    <div className="form-group">
                        <label className="text-dark">Quantity in Stock</label>
                        <input type="text" name="quantity"  value={formData.quantity} readOnly={formState === 'edit' && user.role !== 'admin'} onChange={onChange} className="form-control" />
                    </div>

                    <div className="form-group">
                        <label className="text-dark">Minimum Alerts Threshold</label>
                        <input type="text" min="0" name="min_quantity"  value={formData.min_quantity} onChange={onChange} className="form-control" />
                    </div>
   
                 
                </div>
              </div>
              <button type="submit" className="btn btn-primary">SAVE</button>
            </form>
        </>
    );




    return (
        <div className="d-flex flex-column-fluid pt-5">
            <div className="container-fluid pt-5">
                <div className="row">
                    <div className="col-12">
                        <div className="row">
                            <div className="col-lg-12 col-xl-12">
                                <div className="card card-custom gutter-b bg-transparent shadow-none border-0">
                                <div className="card-header align-items-center  border-bottom-dark px-0">
                                 
                                 {/* Page Title */}
                                <div className="card-title mb-0">
                                    <h3 className="card-label mb-0 font-weight-bold text-body">PRODUCTS</h3>
                                </div>

                                 {/* Actions */}
                                <div className="icons d-flex">
                                    { user.role === 'admin' ?
                                    <button className="btn ml-2 p-0"  onClick={() => addProduct()}>
                                        <span className="bg-dark h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle shadow-sm ">
                                            <i className="fa fa-plus text-white"></i> 
                                        </span>
                                    </button>
                                    : null }
                                
                                    <Link to="#"  className="ml-2" onClick={()=> printProducts()}>
                                        <span className="icon h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle ">
                                            <svg width="15px" height="15px" viewBox="0 0 16 16" className="bi bi-printer-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                            <path d="M5 1a2 2 0 0 0-2 2v1h10V3a2 2 0 0 0-2-2H5z" />
                                            <path fillRule="evenodd" d="M11 9H5a1 1 0 0 0-1 1v3a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1v-3a1 1 0 0 0-1-1z" />
                                            <path fillRule="evenodd" d="M0 7a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v3a2 2 0 0 1-2 2h-1v-2a2 2 0 0 0-2-2H5a2 2 0 0 0-2 2v2H2a2 2 0 0 1-2-2V7zm2.5 1a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z" />
                                            </svg>
                                        </span>
                                    </Link>
{/* 
                                    <Link to="#" className="ml-2">
                                        <span className="icon h-30px font-size-h5 w-30px d-flex align-items-center justify-content-center rounded-circle ">
                                            <svg width="15px" height="15px" viewBox="0 0 16 16" className="bi bi-file-earmark-text-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                            <path fillRule="evenodd" d="M2 2a2 2 0 0 1 2-2h5.293A1 1 0 0 1 10 .293L13.707 4a1 1 0 0 1 .293.707V14a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V2zm7 2l.5-2.5 3 3L10 5a1 1 0 0 1-1-1zM4.5 8a.5.5 0 0 0 0 1h7a.5.5 0 0 0 0-1h-7zM4 10.5a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5zm0 2a.5.5 0 0 1 .5-.5h4a.5.5 0 0 1 0 1h-4a.5.5 0 0 1-.5-.5z" />
                                            </svg>
                                        </span>
                                    </Link>
                                */}
                                </div>
                                </div>
                            </div>
                            </div>
                        </div>

                        {/* List Page */}
                        <div className="row">
                            <div className="col-12 ">
                                <div className="card card-custom gutter-b bg-white border-0">
                                    <div class="card-body">
                                        <div class="form-group row mb-0">
                                            <div class="col-md-12">
                                                <label><b>PRODUCTS CATALOGUE</b></label>
                                                <fieldset class="form-group mb-0 d-flex barcodeselection">
                                                    <input type="text" className="form-control border-dark" onChange={(e)=> keywordSearch(e)} placeholder="SEARCH PRODUCTS ..."/>
                                                </fieldset>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="card-body">
                                       { ListPage() }
                                    </div>
                                </div>
                            </div>
                        </div>

                         {/* Form Page */}
                        <div id="kt_notes_panel" className={showForm === true ? 'offcanvas offcanvas-right kt-color-panel p-5 offcanvas-on':'offcanvas offcanvas-right kt-color-panel p-5'}>
                            {FormPage()}
                        </div>
                       
                    </div>
                </div>
            </div>
        </div>
       
    )
}




export default withCookies(ProductPage);

