import PosProduct from '../components/PosProduct';
import PosCart from '../components/PosCart';
import PosCharge from '../components/PosCharge';
import PosFooter from '../components/PosFooter';
import PosHeader from '../components/PosHeader';
import PosReceipt from '../components/PosReceipt';
import PosRequist from '../components/PosRequist';
import InvoiceCSS from '../components/InvoiceCss';
import Logo from '../assets/images/misc/logo.png';
import Paid from '../assets/images/paid1.jpeg';
import { useState,useEffect, useRef, useContext,useCallback } from 'react';
import { SocketContext} from '../context/socket';
import { withCookies } from "react-cookie";
import moment from 'moment';
import htmlParser from 'html-react-parser'
import PosClient from './PosClient';
import { useReactToPrint } from 'react-to-print';




const PosBody = (props) => {
   const [ msg, setMsg ] = useState({ data:'',class:'' });
   const [ newReq, setNewReq ] = useState(true);
   const [ isOnline, setIsOnline ] = useState(true);
   const [ salesId, setSalesId ] = useState('');
   const [ orders, setOrders ] = useState('');
   const [ keyword, setKeyword ] = useState('');
   const [ clientKeyword, setClientKeyword ] = useState('');
   const [ sessions, setSessions ] = useState([]);
   const [ sessionIndex, setSessionIndex ] = useState(-1);
   const [ cart, setCart ] = useState([]);
   const [ products, setProducts ] = useState([]);
   const [ filteredProducts, setFilteredProducts ] = useState([]);
   const [ showSlider, setShowSlider ] = useState(false);
   const [ formData, setFormData ] = useState({});
   /* Client */
   const [ clients, setClients ] = useState([]);
   const [ filteredClients, setFilteredClients ] = useState([]);
   const [ clientFormData, setClientFormData ] = useState({});
   const [ clientFormState, setClientFormState ] = useState('add');
   const [ showClientForm, setShowClientForm ] = useState(false);
   const [ leftView, setLeftView ] = useState('products');
   const [ client, setClient ] = useState({name:'', id:''});

   const user = props.cookies.get("user");
   const { REACT_APP_API_URL } = process.env;
   const socket = useContext(SocketContext);
   var cartEndRef = useRef();
   var printRef = useRef();

   const handlePrint = useReactToPrint({
     content: () => printRef.current,
   });
   
   // Util Functions
   const useInterval = (callback, delay) => {
         const savedCallback = useRef();
         useEffect(() => {
            savedCallback.current = callback;
         }, [callback]);
         useEffect(() => {
            function tick() { savedCallback.current(); }
            if (delay !== null) { let id = setInterval(tick, delay);return () => clearInterval(id); }
         }, [delay]);
   }
   

   const scrollToBottom = () => {
      cartEndRef.scrollIntoView({ behavior: "smooth" });
   }
  
   const cartEnd = (el) => {
      cartEndRef = el;
   }


   /* Print Receipt */
   useEffect(()=>{
      handlePrint();
   },[])

   /* Initialize Data */
   useEffect(()=>{
      setLoadCart();
      getProducts();
      getOrders();
      getClients();
   },[]);

   /* Store Data */
   useEffect(()=>{
      localStorage.setItem('clients', JSON.stringify(clients));
   },[clients]);
  
     
   /* Timer */
   useInterval(() => {
      setOnline()
   },2000);

  
   /* Sockets Calls */
   useEffect(() => {
      socket.emit("LOAD_PRODUCTS"); 
      socket.on("RECEIVE_PRODUCTS", handleReceive); 
      return () => {
        socket.off("RECEIVE_PRODUCTS", handleReceive); 
      };
   }, [socket]);

   const handleReceive = useCallback((data) => {
        pollProducts(data);
   }, []);


   /* Fetch LocalStorage */
   const setLoadCart = async () => {
      const xs = await loadSessions();
      const ms = await loadCart();
      const ps = await loadProducts();
      const vs = await loadSessionIndex();
      const os = await loadOrders();
      const cs = await loadClients();
      if(ms)  setCart(ms);
      if(ps)  setProducts(ps);
      if(xs)  setSessions(xs);
      if(vs)  setSessionIndex(vs);
      if(os)  setOrders(os);
      if(cs)  setClients(cs);
   }

   /* Fetch New Products */
   const getProducts = async () => {
      const usersFromServer = await fetchProducts();
      if(usersFromServer.success){ 
         setProducts(usersFromServer.data);
         setFilteredProducts(usersFromServer.data);
         localStorage.setItem('products', JSON.stringify(usersFromServer.data));
      }
   }

   /* Fetch New Products */
   const getClients = async () => {
      const clientsFromServer = await fetchClients();
      if(clientsFromServer.success){ 
         setClients(clientsFromServer.data);
         setFilteredClients(clientsFromServer.data);
      }
   }

   /* Poll Products  */
   const pollProducts = async (data) => {
      const usersFromServer = data ? data : [];
      if(usersFromServer.success){ 
         setProducts(usersFromServer.data);
         var pros = usersFromServer.data.filter( product => product.title.trim().toLowerCase().includes(keyword.toLowerCase()));
         setFilteredProducts(pros);
         localStorage.setItem('products', JSON.stringify(usersFromServer.data));
         // Update Price & Max quantities in cart
         var newcart = JSON.parse(localStorage.getItem('cart'))
         if(newcart && newcart.length > 0){
            for(var p of usersFromServer.data){
               newcart = newcart.map((ct,i) => {
                  if(ct.title.trim().toLowerCase() === p.title.trim().toLowerCase()){
                     ct.price = p.price;
                     ct.isDirty = ct.quantity == 0 || p.price == 0 || p.quantity == 0;
                     ct.isActive = (ct.quantity > 0 && p.price > 0 && p.quantity > 0 && i == newcart.length - 1);
                     return ct;
                  }  return ct;
               });
            }  setCart(newcart);
         }
      }
   }


   /* Recent Orders Fetch */
   const getOrders = async () => {
      const usersFromServer = await fetchOrders();
      if(usersFromServer.success) {
         setOrders(usersFromServer.data);
         localStorage.setItem('orders', JSON.stringify(usersFromServer.data));
      }
   }


   /* App Online check */
   const setOnline = ()=> {
      setIsOnline(navigator.onLine);
   }

   // Load Cart Data
   const loadCart = async () => {
       let data = await JSON.parse(localStorage.getItem('cart'));
       return data;
   }

   // Load Cart Data
   const loadProducts = async () => {
      let data = await JSON.parse(localStorage.getItem('products'));
      return data;
   }

    // Load Client Data
    const loadClients = async () => {
      let data = await JSON.parse(localStorage.getItem('clients'));
      return data;
   }

   // Load Session Data
   const loadSessions = async () => {
      let data = await JSON.parse(localStorage.getItem('sessions'));
      return data;
   }

   // Load Session Index Data
   const loadSessionIndex = async () => {
      let data = await JSON.parse(localStorage.getItem('sessionIndex'));
      return data;
   }

   // Load Orders
   const loadOrders = async () => {
      let data = await JSON.parse(localStorage.getItem('orders'));
      return data;
   }

   // Save Data into Storage
   /*
   const saveData = async () => {
       await localStorage.setItem('cart',JSON.stringify(cart));
       await localStorage.setItem('orders',JSON.stringify(orders));
       await localStorage.setItem('sessions',JSON.stringify(sessions));
       await localStorage.setItem('products',JSON.stringify(products));
       await localStorage.setItem('sessionIndex',JSON.stringify(sessionIndex));
   }
   */

   // Fetch Orders
   const fetchOrders = async () => {
       const res = await fetch(`${REACT_APP_API_URL}/dailysales`,{ mode:'cors' })
       const data = await res.json();
       return data
   }


   // Fetch Products
   const fetchProducts = async () => {
      const res = await fetch(`${REACT_APP_API_URL}/products`,{ mode:'cors' })
      const data = await res.json();
      var mdata = data.data.map((product) => {
          product.max = product.quantity;
          return product;
      })
      return {...data,data:mdata};
   }

   // Fetch Clients
   const fetchClients = async () => {
      const res = await fetch(`${REACT_APP_API_URL}/clients`,{ mode:'cors' })
      const data = await res.json();
      return data;
   }


   // Add Product to Cart
   const addToCart = (id) => {
       var ct = cart.find( ct => ct._id === id);
       var product = products.find( product => product._id === id);
       if(product && !ct){
         /* 
         if(product.max > 0 && product.price > 0){   */

            var newcart = cart.map((ct,i) => { 
               ct.isActive = false;
               return ct;
            })
            setCart([...newcart, {...product,quantity: product.max > 0 ? 1:0, isDirty: (product.price == 0 || product.quantity == 0 ? true: false), isActive : true } ]);
            localStorage.setItem('cart', JSON.stringify([...newcart, {...product,quantity: product.max > 0 ? 1:0,  isDirty: (product.price == 0 || product.quantity == 0 ? true: false), isActive : true } ]));
            scrollToBottom();
         /*
         }else{
            const dmesg = { data : htmlParser("<b>PRODUCT OUT OF STOCK OR PRICE NOT SET FOR PRODUCT !</b>"), class : 'd-flex justify-content-between alert text-center alert-danger' }
            setMsg({...dmesg });
            setTimeout(() => {
                setMsg({ data:'', class:'' })
            },2000)
         }
         */
       }
    }

    const removeFromCart = (id) => {
       var ct = cart.filter( ct => ct._id !== id);
       setCart(ct);
       localStorage.setItem('cart', JSON.stringify(ct));
    }  

    const clearOrder = () => {
      var ok = window.confirm("CLEARING CART !")
      if(ok){
         setCart([]);
         setSessionIndex(-1);
         setSalesId('')
         localStorage.setItem('cart', JSON.stringify([]));
         localStorage.setItem('sessionIndex', -1);
      }
    }

    const saveOrder = () => {
      console.log(cart);
    }

    const newSale = () => {
      setShowSlider(false);
    }

    const onChange = (e,id) => {
       var ct = cart.filter( ct => ct._id === id);
       var index = cart.findIndex((ct => ct._id === id));
       let newcart = [...cart];
       newcart[index].quantity = e.target.value <= newcart[index].max ? e.target.value : 0;
       e.target.value = e.target.value <= newcart[index].max ? e.target.value : 0;
       newcart[index].isDirty = e.target.value == 0 || newcart[index].price == 0 ? true : false;

       if(ct.length > 0){ 
          setCart(newcart);
          localStorage.setItem('cart', JSON.stringify(newcart));
       }  
    }

    const payCredit = async() => {
      setNewReq(false); 
      if(cart.length <= 0){
         const dmesg = { data : htmlParser("<b>CART IS EMPTY - PLEASE SELECT ATLEAST A PRODUCT !</b>"), class : 'd-flex justify-content-between alert text-center alert-danger' }
         setMsg({...dmesg });
         setTimeout(() => {
             setMsg({ data:'', class:'' })
         },2000)
      }else{
         var isOk = true;
         for(var ct of cart){
            if(ct.quantity <= 0) isOk = false;
         }

         if(!isOk){
            const dmesg = { data : htmlParser("<b>ZERO(O) ITEM QUANTITY DETECTED - PLEASE ENTER QUANTITIES OF PRODUCTS !</b>"), class : 'd-flex justify-content-between alert text-center alert-danger' }
            setMsg({...dmesg });
            setTimeout(() => {
              setMsg({ data:'', class:'' })
            },2000)
            return false;
         }else{
            var ok = window.confirm("MAKE CREDIT PAYMENT ?\n -- Note that this action will reduce stock quantities!");
            if(ok){
               // Send Order to Server
               //var refname = window.prompt('Enter name of Customer!')
               if(client.name == ''){
                  setLeftView('clients');
                  return false;
               }
               try{
                  var formDatax = {cart,user:user.name, amount: cart.reduce((acc,ct) => (ct.price*ct.quantity)+acc, 0),refname: client.name && client.name.toUpperCase()};
                  const body = JSON.stringify(formDatax);
                  setFormData({...formData, formDatax});
                  const url = `${REACT_APP_API_URL}/creditorder${salesId === '' ? "": "/"+salesId}`;
                  const res = await fetch(url,{
                        headers: {"Content-Type": "application/json"},
                        mode:    "cors",
                        method:  salesId !== '' ? "PUT" : "POST",
                        body:    body
                  });
                  
                  const data = await res.json();
                  getOrders();
                  getProducts();
                  
                  if(data.success){
                     
                     setFormData({...formData, oid:data.data.oid,ordertype:data.data.ordertype,refname: data.data.refname,created_at:data.data.created_at});
                     setShowSlider(true); 
                  
                     // Remove Session record
                     if(sessionIndex > -1){
                        const sess = sessions.filter((s,i) => i !== sessionIndex )
                        setSessions(sess);
                        localStorage.setItem('sessions', JSON.stringify(sess));
                     }
                     
                     setTimeout(()=>{
                        var content = document.getElementById("print");
                        var pri = document.getElementById("printer");
                        const style = document.createElement("style");
                        style.innerHTML = InvoiceCSS;
                        pri.contentWindow.document.open();
                        pri.contentWindow.document.write(content.innerHTML);//1
                        pri.contentWindow.document.write(content.innerHTML);//2
                        pri.contentDocument.head.appendChild(style)
                        pri.contentWindow.document.close();
                        pri.contentWindow.focus();
                        pri.contentWindow.print();
                        setCart([]); 
                        localStorage.setItem('cart', JSON.stringify([]));
                        setShowSlider(false); 
                     },3000);

                     const dmesg = { data : htmlParser(`<b>${salesId == '' ? 'CREDIT SALE SUCCESSFULL !': ( data.data.ordertype == 'normal' ? 'CASH' : data.data.ordertype.toUpperCase())+' SALE UPDATE SUCCESSFUL !'}</b><small><b>RECEIPT : # ${data.data.oid}</b></small>`), class : 'd-flex justify-content-between alert text-center alert-success' }
                     setMsg({...dmesg });
                     setTimeout(() => {
                     setMsg({ data:'', class:'' })
                     },10000)
                     setSalesId('');
                     setLeftView('products');
                     setClient({ name:'', id:'' });

                  }else{
                     // Error on Server Side
                     const dmesg = { data : htmlParser(`<b>${data.msg.toString().toUpperCase()} !</b>`), class : 'd-flex justify-content-between alert text-center alert-danger' }
                     setMsg({...dmesg });
                     setTimeout(() => {
                     setMsg({ data:'', class:'' })
                     },2000)
                  }
   
               } catch (err) {
                  const dmesg = { data : htmlParser(`<b>${err.toString().toUpperCase()} !</b>`), class : 'd-flex justify-content-between alert text-center alert-danger' }
                  setMsg({...dmesg });
                  setTimeout(() => {
                  setMsg({ data:'', class:'' })
                  },2000)
               }
            }
         }
      }
    }

    const payCash = async () => {
      setNewReq(false); 
      if(cart.length <= 0){
         const dmesg = { data : htmlParser("<b>CART IS EMPTY - PLEASE SELECT ATLEAST A PRODUCT !</b>"), class : 'd-flex justify-content-between alert text-center alert-danger' }
         setMsg({...dmesg });
         setTimeout(() => {
             setMsg({ data:'', class:'' })
         },2000)
      }else{
         var isOk = true;
         for(var ct of cart){
            if(ct.quantity <= 0) isOk = false;
         }
         if(!isOk){
            const dmesg = { data : htmlParser("<b>ZERO(O) ITEM QUANTITY DETECTED - PLEASE ENTER QUANTITIES OF PRODUCTS !</b>"), class : 'd-flex justify-content-between alert text-center alert-danger' }
            setMsg({...dmesg });
            setTimeout(() => {
              setMsg({ data:'', class:'' })
            },2000)
            return false;
         }else{
            var ok = window.confirm("MAKE CASH PAYMENT ?\n- Note that this action will reduce stock quantities!");
            if(ok){
               var refname = window.prompt('Enter name of Customer!')
               // Send Order to Server
               try{
                  var formDatax = {cart,user:user.name, amount: cart.reduce((acc,ct) => (ct.price*ct.quantity)+acc, 0), refname: refname && refname.toUpperCase()};
                  const body = JSON.stringify(formDatax);
                  setFormData({...formData, formDatax});
                  const url = `${REACT_APP_API_URL}/cashorder${salesId === '' ? "": "/"+salesId}`;
                  const res = await fetch(url,{
                        headers: {"Content-Type": "application/json"},
                        mode:    "cors",
                        method:  salesId !== '' ? "PUT" : "POST",
                        body:    body
                  });
                 
                  const data = await res.json();
                  getOrders();
                  getProducts();
                  
                  if(data.success){
                     setFormData({...formData, oid:data.data.oid,ordertype:data.data.ordertype,refname: data.data.refname,created_at:data.data.created_at});
                     setShowSlider(true); 
                     // Remove Session record
                     if(sessionIndex > -1){
                        const sess = sessions.filter((s,i) => i !== sessionIndex )
                        setSessions(sess);
                        localStorage.setItem('sessions', JSON.stringify(sess));
                     }  
                     setTimeout(() => {
                        /**/
                        var content = document.getElementById("print");
                        //content.removeChild(content.childElementCount-1)
                        //content.querySelector('#rlogo').setAttribute('src',Logo)
                        //console.log(content.querySelector('#rlogo').getAttribute('src'))
                        var pri = document.getElementById("printer");
                        const style = document.createElement("style");
                        style.innerHTML = InvoiceCSS;
                        pri.contentWindow.document.open();
                        pri.contentWindow.document.write(content.innerHTML);//1
                        pri.contentWindow.document.write(content.innerHTML);//2
                        pri.contentDocument.head.appendChild(style)
                        //console.log(pri.contentDocument.body)
                        pri.contentWindow.document.close();
                        pri.contentWindow.focus();
                        pri.contentWindow.print();
                        
                        
                        //handlePrint();

                        setCart([]); // Reset Cart
                        localStorage.setItem('cart', JSON.stringify([]));
                        setShowSlider(false); // Hide Receipt Slider
                     },2000);

                     const dmesg = { data : htmlParser(`<b>${salesId == '' ? 'CASH SALE SUCCESSFULL !': ( data.data.ordertype == 'normal' ? 'CASH' : data.data.ordertype.toUpperCase())+' SALE UPDATE SUCCESSFUL !'}</b><small><b>RECEIPT : # ${data.data.oid}</b></small>`), class : 'd-flex justify-content-between alert text-center alert-success' }
                     setMsg({...dmesg });
                     setTimeout(() => {
                     setMsg({ data:'', class:'' })
                     },10000)
                     setSalesId('');

                  }else{
                     // Error on Server Side
                     const dmesg = { data : htmlParser(`<b>${data.msg.toString().toUpperCase()} !</b>`), class : 'd-flex justify-content-between alert text-center alert-danger' }
                     setMsg({...dmesg });
                     setTimeout(() => {
                     setMsg({ data:'', class:'' })
                     },2000)
                  }
  
               } catch (err) {
                  const dmesg = { data : htmlParser(`<b>${err.toString().toUpperCase()} !</b>`), class : 'd-flex justify-content-between alert text-center alert-danger' }
                  setMsg({...dmesg });
                  setTimeout(() => {
                  setMsg({ data:'', class:'' })
                  },2000)
               }
            }
         }
      }
      
    }

    const makeInvoice = async () => {
      setNewReq(false); 
      var ok = window.confirm("GENERATE INVOICE ?\n- Note that this action does not affect sales!");
      if(ok){
         var refname = window.prompt('Enter name of Customer!')
         var formDatax = {cart,user:user.name, amount: cart.reduce((acc,ct) => (ct.price*ct.quantity)+acc, 0), refname: refname && refname,oid:moment().unix(),ordertype:'invoice',created_at:moment().format('LLL')};
         var newdata  = {...formData,...formDatax};
         console.log(newdata);
         setFormData(newdata);
         setShowSlider(true); 
         setSalesId('');
         setTimeout(() => {
            var content = document.getElementById("print");
            var pri = document.getElementById("printer");
            const style = document.createElement("style");
            style.innerHTML = InvoiceCSS;
            pri.contentWindow.document.open();
            pri.contentWindow.document.write(content.innerHTML);//1
            pri.contentWindow.document.write(content.innerHTML);//2
            pri.contentDocument.head.appendChild(style)
            pri.contentWindow.document.close();
            pri.contentWindow.focus();
            pri.contentWindow.print();
            //setCart([]); // Reset Cart
            //localStorage.setItem('cart', JSON.stringify([]));
            setShowSlider(false); // Hide Receipt Slider
         },4000);
      }
    }


    const makeRequest = async () => {
         var formDatax = {cart,user:user.name, amount: cart.reduce((acc,ct) => (ct.price*ct.quantity)+acc, 0),oid:moment().unix(),ordertype:'credit',created_at:moment().format('LLL')};
         var newdata  = {...formData,...formDatax};
         setFormData(newdata);
         setShowSlider(true); 
         setNewReq(true); 
         setSalesId('');
         setTimeout(() => {
            var content = document.getElementById("print");
            var pri = document.getElementById("printer");
            const style = document.createElement("style");
            style.innerHTML = InvoiceCSS;
            pri.contentWindow.document.open();
            pri.contentWindow.document.write(content.innerHTML);//1
            pri.contentWindow.document.write(content.innerHTML);//2
            pri.contentDocument.head.appendChild(style)
            pri.contentWindow.document.close();
            pri.contentWindow.focus();
            pri.contentWindow.print();
            //setCart([]); // Reset Cart
            //localStorage.setItem('cart', JSON.stringify([]));
            setShowSlider(false); // Hide Receipt Slider
         },4000);
    }


    const keywordSearch = (e) => {
      setKeyword(e.target.value.trim());
      var pros = products.filter( product => product.title.trim().toLowerCase().includes(e.target.value.trim().toLowerCase()));
      setFilteredProducts(pros);
    }

    const clientKeywordSearch = (e) => {
      setClientKeyword(e.target.value.trim());
      var pros = clients.filter( ct => ct.name.trim().toLowerCase().includes(e.target.value.trim().toLowerCase()) || (ct.phone && ct.phone.trim().toLowerCase().includes(e.target.value.trim().toLowerCase())) || (ct.location && ct.location.trim().toLowerCase().includes(e.target.value.trim().toLowerCase())) );
      setFilteredClients(pros);
    }

    const categorySearch = (e) => {
      setFilteredProducts(products);
    }
    
    // Sessions and Saved Orders
    const saveSession = () => {
      var tag = window.prompt('TAG CLIENT\'s SESSION!');
      if(tag) {
         const data = [...sessions,{tag,cart}];
         setSessionIndex(-1);
         setSessions(data);
         setCart([]);
         localStorage.setItem('sessionIndex', -1);
         localStorage.setItem('sessions', JSON.stringify(data));
         localStorage.setItem('cart', JSON.stringify([]));
      }
    }

    // Sessions and Saved Orders
    const resumeSession = (index) => {
      const session = sessions[index]
      if(session) {
         setSessionIndex(index);
         setCart(session.cart);
         localStorage.setItem('sessionIndex', index);
         localStorage.setItem('cart', JSON.stringify(session.cart));
      }
   }

   const clearSession = () => {
      var ok = window.confirm("CLEARING SAVED SESSIONS !")
      if(ok){
         setSessionIndex(-1);
         setSessions([]);
         localStorage.setItem('sessionIndex', -1);
         localStorage.setItem('sessions', JSON.stringify([]));
      }
   }

   // Orders and Last Ten Sales
   const updateOrder = (id) => {
      var ok = window.confirm("LOAD SALE RECORD !")
      if(ok){
         var order = orders.find((o => o._id === id));
         let ct = order.cart.map((c) => {
            c.max = c.product.quantity;
            c.title = c.product.title;
            c._id = c.product._id;
            return c;
         })
         setCart(ct);
         setSalesId(id);
         scrollToBottom();
      }
   }

   
   // Delete Client
   const delClient = async (id)=>{
      var ok = window.confirm("Delete Client ? \n- This will remove an account user!");
      if(ok){
          const res = await fetch(`${REACT_APP_API_URL}/clients/${id}`, {
              mode: 'cors',
              method: 'DELETE',
          })
          const data = await res.json();
          const clientsFromServer = await fetchClients();
          if(clientsFromServer.success){
             setClients(clientsFromServer.data);
             setFilteredClients(clientsFromServer.data);
          }
      }
   }

   // Send Credit Sale
   const sendCreditSale = async (id)=> {
      var ok = window.confirm("Apply Sale to Client ? \n- This will assign current sale to customer!");
      if(ok){
         const newClients = [...clients];
         const cl = newClients.find((c) => c._id === id);
         setClient({...client,name:cl.name, id: cl._id})
      }
   }


   // Select Client
   const selClient = async (id)=> {
      const newClients = [...clients];
      const cl = newClients.find((c) => c._id === id);
      setClient(cl)  
   }

  // Edit Client
  const editClient = async (id)=>{
      const res = await fetch(`${REACT_APP_API_URL}/clients/${id}`, {mode: 'cors'})
      const data = await res.json();
      if(data.success){ setClientFormData(data.data); }
      setClientFormState('edit');
      setShowClientForm(true);
  }

  // Add User
  const addClient = () => {
      window.document.getElementById("clientform").reset();
      setClientFormData({});
      setClientFormState('add');
      setShowClientForm(true);
  }
  
  // Client Input handler
  const onClientChange = (e) => setClientFormData({ ...clientFormData, [e.target.name]: e.target.value });

  // Save Client 
  const onClientSubmit = async (e) => {
      e.preventDefault();
      try{
         const body = JSON.stringify(clientFormData);
         const url = `${REACT_APP_API_URL}/clients${clientFormState === 'add' ? "": "/"+clientFormData._id}`;
         const method = clientFormState === 'add' ? "POST": "PUT";
         const res = await fetch(url,{
             headers: {"Content-Type": "application/json"},
             mode:    "cors",
             method:  method,
             body:    body
         });
         if(res.status === 200){
             const clientsFromServer = await fetchClients();
             if(clientsFromServer.success){
               setClients(clientsFromServer.data);
               setFilteredClients(clientsFromServer.data);
               setShowClientForm(false);
               setFormData({});
             }
         }    
      } catch (err) {
         console.error(err);
      }
  };


  /* PAGES */
  const ClientFormPage = () => (
      <>
          <div className="offcanvas-header d-flex align-items-center justify-content-between pb-3">
              <h4 className="font-size-h4 font-weight-bold m-0">{ clientFormState === 'add' ? 'ADD CLIENT':'EDIT CLIENT'} <hr/></h4>
              <button className="btn btn-sm btn-icon btn-light btn-hover-primary p-2" onClick={() => setShowClientForm(false)}><i className="fa fa-times"></i></button>
          </div>

          <form id="clientform" onSubmit={(e) => onClientSubmit(e)} noValidate={true}>
            <div className="row">
              <div className="col-12">
                  <div className="form-group">
                      <label className="text-dark">Full Name</label>
                      <input type="text" name="name"  value={clientFormData.name} onChange={onClientChange} className="form-control" />
                  </div>
                  <div className="form-group">
                      <label className="text-dark">Email</label>
                      <input type="email" name="email" value={clientFormData.email} onChange={onClientChange} className="form-control"/>
                  </div>
                  <div className="form-group">
                      <label className="text-dark">Phone Number</label>
                      <input type="text" name="phone" value={clientFormData.phone} onChange={onClientChange} className="form-control" />
                  </div>
                  <div className="form-group">
                      <label className="text-dark">Address</label>
                      <input type="text" name="address" value={clientFormData.address} onChange={onClientChange} className="form-control valid" aria-invalid="false" />
                  </div>
                  <div className="form-group">
                      <label className="text-dark">Location</label>
                      <input type="text" name="location" value={clientFormData.location} onChange={onClientChange} className="form-control valid" required aria-invalid="false" />
                  </div>
              </div>
            </div>
            <button type="submit" className="btn btn-primary">SAVE</button>
          </form>
      </>
  );




    return (
      <>
       <PosHeader user={props.user} sessions={sessions} resumeSession={resumeSession} orders={orders} updateOrder={updateOrder} isOnline={isOnline} logout={props.logout}/>
      <div className="contentPOS" ref={props.printEnd}>
         <div className="container-fluid">
            <div className="row">
                { leftView !== 'products' ?
                 <PosClient addClient={addClient} editClient={editClient} delClient={delClient} selClient={selClient} payCredit={payCredit} filteredClients={filteredClients} clientKeywordSearch={clientKeywordSearch} showClientForm={showClientForm} clientFormState={clientFormState} content={ClientFormPage} client={client}/>
                 :
                 <PosProduct addToCart={addToCart} products={filteredProducts} keywordSearch={keywordSearch} categorySearch={categorySearch}/>
                }
                 <PosCart cart={cart} removeFromCart={removeFromCart} onChange={onChange} payCredit={payCredit} payCash={payCash} clearSession={clearSession} saveSession={saveSession} makeInvoice={makeInvoice} makeRequest={makeRequest} cartEnd={cartEnd} msg={msg}/>
                 <PosCharge cart={cart} saveOrder={saveOrder} clearOrder={clearOrder} sessionIndex={sessionIndex}  salesId={salesId} client={client}/>
                 <PosFooter showSlider={showSlider} content={ newReq ? PosRequist : PosReceipt } cart={cart} newSale={newSale} oid={formData.oid} ordertype={formData.ordertype} refname={formData.refname} created_at={moment(formData.created_at).format('LLL')} />
            </div>
         </div>
      </div>
      </>
    )
}

export default withCookies(PosBody);
